import React from 'react';
import {Button, Grid} from "@material-ui/core";
import "./style.scss";
import Input from "@material-ui/core/Input";
import connect from 'react-redux/es/connect/connect';
import {bindActionCreators} from 'redux';
import {onChangePersonalDetails} from '../../../../../modules/action';


class PersonalDetail extends React.Component {

  onChange = (event) => {
    this.props.onChangePersonalDetails({
      [event.target.name]: event.target.value,
    })
  };

  render() {
    const {user} = this.props;
    return (
      <Grid item container justify="space-between" className="PersonalDetails">
        <Grid container alignItems="center" justify='space-between'>
          <Grid item xs={5} className="personalDetails">
            <h3>Personal Detail</h3>
            <p>Your personal information is never to other users.</p>
          </Grid>
          <Grid item xs={7} container justify="space-between" className="legalName">
            <Grid className="label">
              <p>Legal Name</p>
            </Grid>
            <Grid item style={{width: '48%'}}>
              <Input
                fullWidth
                name="firstname"
                type="text"
                className="inputs"
                value={user.firstname}
                onChange={this.onChange}
              />
            </Grid>

            <Grid item style={{width: '48%'}}>
              <Input
                fullWidth
                name="lastname"
                type="text"
                className="inputs"
                value={user.lastname}
                onChange={this.onChange}
              />
            </Grid>
          </Grid>
        </Grid>
        <Grid container alignItems="center" justify="flex-end" className="birthBlock">
          <Grid item xs={7} container justify="space-between" className="birthday">
            <Grid className="label">
              <p>Date of birth</p>
            </Grid>
            <Grid item xs={5} className="day">
              <Input
                fullWidth
                inputProps={{min: "1", max: "12", step: "1"}}
                name="month"
                placeholder="Month"
                type="number"
                className="inputs"
                value={user.month}
                onChange={this.onChange}
              />
            </Grid>
            <Grid item xs={3}>
              <Input
                fullWidth
                inputProps={{min: "1", max: "31", step: "1"}}
                name="date"
                type="number"
                placeholder="Day"
                className="inputs"
                value={user.date}
                onChange={this.onChange}
              />
            </Grid>
            <Grid item xs={3}>
              <Input
                fullWidth
                inputProps={{min: "1900", max: "2019", step: "1"}}
                name="year"
                type="number"
                className="inputs"
                placeholder="Year"
                value={user.year}
                onChange={this.onChange}
              />
            </Grid>

          </Grid>
          <Grid container justify='flex-end'>
            <Button className='btnSave' variant="contained">Save</Button>
          </Grid>
        </Grid>
      </Grid>
    )
  }
}

const mapStateToProps = store => ({
  user: store.user.settings
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onChangePersonalDetails
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(PersonalDetail);
