import React, {Component} from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import connect from 'react-redux/es/connect/connect';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem/MenuItem';
import Button from '@material-ui/core/Button/Button';
import copy from '../../../../../../images/copy.png'

class Transfer extends Component {
    state = {
        amount: 0,
        currency: 'BTC',
        referenceNumber: '',
        bankName: '',
        recipientName: '',
        iban: '',
        bankCountry: '',
        bankAddress: '',
        recipientAddress: '',
        switchBic: ''
    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };

    render() {
        const {coins} = this.props;
        return (
            <Grid className="transfert">
                <form className='card-form'>
                    <Grid className="content">
                        <h3 className="subtitle">Transfer</h3>
                        <Grid container
                              justify="space-between"
                              className="selectandinput"
                              wrap="nowrap"
                              direction="row">
                            <Grid container
                                  direction="column">
                                <span className="label">AMOUNT</span>
                                <input
                                    type="number"
                                    name="amount"
                                    required
                                    value={this.state.amount}
                                    onChange={this.handleChange} />
                            </Grid>
                            <Grid container
                                  direction="column">
                                <span className="label">CURRENCY</span>
                                <Select
                                    className="selectCurrency"
                                    value={this.state.currency}
                                    onChange={this.handleChange}
                                    inputProps={{
                                        name: 'currency'
                                    }}>
                                    {Object.keys(coins).map((key, id) => {
                                        const item = coins[key];
                                        return <MenuItem className="labelName"
                                                         key={id}
                                                         value={item.shortName}>
                                            {item.shortName}
                                        </MenuItem>
                                    })}
                                </Select>
                            </Grid>
                        </Grid>
                        <h3 className="subtitle">Recipient</h3>
                        <Grid container
                              className="copyInput"
                              direction="column">
                            <span className="label">REFERENCE NUMBER</span>
                            <input
                                type="text"
                                name="referenceNumber"
                                required
                                value={this.state.referenceNumber}
                                onChange={this.handleChange} />
                            <img src={copy}
                                 alt='icon'
                                 className="copy-icon" />
                        </Grid>
                        <Grid container
                              className="copyInput"
                              direction="column">
                            <span className="label">RECIPIENTS IBAN</span>
                            <input
                                type="text"
                                name="iban"
                                required
                                value={this.state.iban}
                                onChange={this.handleChange} />
                            <img src={copy}
                                 alt='icon'
                                 className="copy-icon" />
                        </Grid>
                        <Grid container
                              direction="column">
                            <span className="label">RECIPIENT NAME</span>
                            <input
                                type="text"
                                name="recipientName"
                                required
                                value={this.state.recipientName}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid container
                              direction="column">
                            <span className="label">BANK NAME</span>
                            <input
                                type="text"
                                name="bankName"
                                required
                                value={this.state.bankName}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid container
                              direction="column">
                            <span className="label">BANK COUNTRY</span>
                            <input
                                type="text"
                                name="bankCountry"
                                required
                                value={this.state.bankCountry}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid container
                              direction="column">
                            <span className="label">BANK ADDRESS</span>
                            <input
                                type="text"
                                name="bankAddress"
                                required
                                value={this.state.bankAddress}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid container
                              direction="column">
                            <span className="label">RECIPIENT ADDRESS</span>
                            <input
                                type="text"
                                name="recipientAddress"
                                required
                                value={this.state.recipientAddress}
                                onChange={this.handleChange} />
                        </Grid>
                        <Grid container
                              className="copyInput"
                              direction="column">
                            <span className="label">SWIFT / BIC</span>
                            <input
                                type="text"
                                name="switchBic"
                                required
                                value={this.state.switchBic}
                                onChange={this.handleChange} />
                            <img src={copy}
                                 alt='icon'
                                 className="copy-icon" />
                        </Grid>
                        <Grid>
                            <Button variant="contained"
                                    type='submit'
                                    className="continueBtn">
                                Continue
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Grid>
        );
    }
}

const mapStateToProps = store => ({
    coins: store.wallets.coins
});

export default connect(mapStateToProps)(Transfer);

