import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Tabs from '@material-ui/core/Tabs/Tabs';
import Tab from '@material-ui/core/Tab/Tab';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography/Typography';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem/MenuItem';
import connect from 'react-redux/es/connect/connect';
import Button from '@material-ui/core/Button/Button';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faPlusCircle, faTimes} from '@fortawesome/free-solid-svg-icons';
import {Link} from 'react-router-dom';

function TabContainer(props) {
    return (
        <Typography component="div">
            {props.children}
        </Typography>
    );
}

TabContainer.propTypes = {
    children: PropTypes.node.isRequired,
};

class BuySell extends React.Component {
    state = {
        value: 0,
        nameWallet: 'BTC',
        shortName: 'BTC',
        countWallet: '0',
        count: '0',
        nameWalletSell: 'BTC',
        shortNameSell: 'BTC',
        countWalletSell: '0',
        countSell: '0',

    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };


    handleChangeTab = (event, value) => {
        this.setState({value});
    };

    render() {
        const {value} = this.state;
        const {coins} = this.props;
        return (
            <Grid className="buy-sell-wrapper">
                <h2 className="title">Buy/Sell</h2>
                <Grid container
                      direction="row"
                      alignItems="flex-start"
                      justify="space-between">
                    <Grid className="tab-content">
                        <Paper className="wrapper">
                            <AppBar position="static">
                                <Tabs value={value}
                                      onChange={this.handleChangeTab}>
                                    <Tab label="Buy"></Tab>
                                    <Tab label="Sell" />
                                </Tabs>
                                <FontAwesomeIcon icon={faTimes}
                                                 className="close-icon" />
                            </AppBar>
                            {value === 0 && <TabContainer>
                                <Grid container
                                      className="tabContainer"
                                      direction="row"
                                      justify="flex-start"
                                      alignItems="center">
                                    <FormControl className="form-control">
                                        <Grid container
                                              className="form-wrapper"
                                              direction="row"
                                              alignItems="center">
                                            <Grid container
                                                  alignItems="flex-end"
                                                  justify="space-between"
                                                  direction="row">
                                                <Grid container
                                                      direction="column">
                                                    <span className="label">PAYMENT METHOD</span>
                                                    <Select
                                                        className="selectCurrency"
                                                        value={this.state.nameWallet}
                                                        onChange={this.handleChange}
                                                        inputProps={{
                                                            name: 'nameWallet'
                                                        }}>
                                                        {Object.keys(coins).map((key, id) => {
                                                            const item = coins[key];
                                                            return <MenuItem className="labelName"
                                                                             key={id}
                                                                             value={item.shortName}>
                                                                My {item.shortName} Wallet
                                                            </MenuItem>
                                                        })}
                                                    </Select>
                                                </Grid>
                                                <Grid className="input-field">
                                                    <span className="label">AMOUNT</span>
                                                    <input
                                                        type="number"
                                                        name="countWallet"
                                                        min={0}
                                                        className="amount"
                                                        value={this.state.countWallet}
                                                        onChange={this.handleChange}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <Link to="/payments" className="linkDecoration">
                                                <p className="add-payment">
                                                    <FontAwesomeIcon icon={faPlusCircle}
                                                                     className="add-icon" />
                                                    <span>Add Payment Method</span>
                                                </p>
                                            </Link>
                                            <Grid container
                                                  alignItems="flex-end"
                                                  justify="space-between"
                                                  direction="row">
                                                <Grid container
                                                      direction="column">
                                                    <span className="label">CURRENCY</span>
                                                    <Select
                                                        className="selectCurrency"
                                                        value={this.state.shortName}
                                                        onChange={this.handleChange}
                                                        inputProps={{
                                                            name: 'shortName'
                                                        }}>
                                                        {Object.keys(coins).map((key, id) => {
                                                            const item = coins[key];
                                                            return <MenuItem className="labelName"
                                                                             key={id}
                                                                             value={item.shortName}>
                                                                {item.shortName}
                                                            </MenuItem>
                                                        })}
                                                    </Select>
                                                </Grid>
                                                <Grid className="input-field">
                                                    <span className="label">AMOUNT</span>
                                                    <input
                                                        type="number"
                                                        name="count"
                                                        min={0}
                                                        className="amount"
                                                        value={this.state.count}
                                                        onChange={this.handleChange}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <p className="message">For <span className="pink"> {this.state.countWallet} {this.state.nameWallet}</span> you will
                                                                   get <span className="pink"> {this.state.count} {this.state.shortName}</span>
                                            </p>
                                            <Grid>
                                                <Button variant="contained"
                                                        className="continueBtn">
                                                    Continue
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </FormControl>
                                </Grid>
                            </TabContainer>}
                            {(value === 1) && <TabContainer>
                                <Grid container
                                      className="tabContainer"
                                      direction="row"
                                      justify="flex-start"
                                      alignItems="center">
                                    <FormControl className="form-control">
                                        <Grid container
                                              className="form-wrapper"
                                              direction="row"
                                              alignItems="center">
                                            <Grid container
                                                  alignItems="flex-end"
                                                  justify="space-between"
                                                  direction="row">
                                                <Grid container
                                                      direction="column">
                                                    <span className="label">SELL FROM</span>
                                                    <Select
                                                        className="selectCurrency"
                                                        value={this.state.shortNameSell}
                                                        onChange={this.handleChange}
                                                        inputProps={{
                                                            name: 'shortNameSell'
                                                        }}>
                                                        {Object.keys(coins).map((key, id) => {
                                                            const item = coins[key];
                                                            return <MenuItem className="labelName"
                                                                             key={id}
                                                                             value={item.shortName}>
                                                                {item.shortName}
                                                            </MenuItem>
                                                        })}
                                                    </Select>
                                                </Grid>
                                                <Grid className="input-field">
                                                    <span className="label">AMOUNT</span>
                                                    <input
                                                        type="number"
                                                        name="countSell"
                                                        min={0}
                                                        className="amount"
                                                        value={this.state.countSell}
                                                        onChange={this.handleChange} />
                                                </Grid>
                                            </Grid>
                                            <p className="spacer"></p>
                                            <Grid container
                                                  alignItems="flex-end"
                                                  justify="space-between"
                                                  direction="row">
                                                <Grid container
                                                      direction="column">
                                                    <span className="label">DEPOSIT TO</span>
                                                    <Select
                                                        className="selectCurrency"
                                                        value={this.state.nameWalletSell}
                                                        onChange={this.handleChange}
                                                        inputProps={{
                                                            name: 'nameWalletSell'
                                                        }}>
                                                        {Object.keys(coins).map((key, id) => {
                                                            const item = coins[key];
                                                            return <MenuItem className="labelName"
                                                                             key={id}
                                                                             value={item.shortName}>
                                                                My {item.shortName} Wallet
                                                            </MenuItem>
                                                        })}
                                                    </Select>
                                                </Grid>
                                                <Grid className="input-field">
                                                    <span className="label">AMOUNT</span>
                                                    <input
                                                        type="number"
                                                        name="countWalletSell"
                                                        className="amount"
                                                        min={0}
                                                        value={this.state.countWalletSell}
                                                        onChange={this.handleChange}
                                                    />
                                                </Grid>
                                            </Grid>
                                            <p className="message">For <span className="pink"> {this.state.countSell} {this.state.shortNameSell}</span> you
                                                                   will get <span className="pink"> {this.state.countWalletSell} {this.state.nameWalletSell}</span></p>
                                            <Grid>
                                                <Button variant="contained"
                                                        className="continueBtn">
                                                    Continue
                                                </Button>
                                            </Grid>
                                        </Grid>
                                    </FormControl>
                                </Grid>
                            </TabContainer>}
                        </Paper>
                    </Grid>

                    {value === 0 && <Grid className="right-block">
                        <h2 className="title">YOU ARE BUYING</h2>
                        <Grid className="buy-info"
                              container
                              alignItems="center"
                              justify="center">
                            <span onChange={this.handleChange}>{this.state.count}</span>
                            <span onChange={this.handleChange}>{this.state.shortName}</span>
                        </Grid>
                        <Grid className="wallet-info">
                            <p className="label">PAYMENT METHOD</p>
                            <p>My <span className="pink">{this.state.nameWallet}</span> Wallet</p>
                            <br />
                            <p className="label">DEPOSIT TO</p>
                            <p>My <span className="pink"> {this.state.shortName}</span> Wallet</p>
                        </Grid>
                        <Grid className="border-block">
                            <p>
                                <span>0,00000000 BTC</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                            <p>
                                <span>FLASHWALLET  Fee</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                            <p>
                                <span>TOTAL</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                        </Grid>
                    </Grid>}

                    {value === 1 && <Grid className="right-block">
                        <h2 className="title">YOU ARE SELLING</h2>
                        <Grid className="buy-info"
                              container
                              alignItems="center"
                              justify="center">
                            <span onChange={this.handleChange}>{this.state.countSell}</span>
                            <span onChange={this.handleChange}>{this.state.shortNameSell}</span>
                        </Grid>
                        <Grid className="wallet-info info-sell">
                            <p className="label">WITHDRAW FROM</p>
                            <p>My <span className="pink">{this.state.nameWalletSell}</span> Wallet</p>
                            <br />
                            <p className="label">DEPOSIT TO</p>
                            <p>My <span className="pink"> {this.state.shortNameSell}</span> Wallet</p>
                        </Grid>
                        <Grid className="border-block">
                            <p>
                                <span>0,00000000 BTC</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                            <p>
                                <span>FLASHWALLET  Fee</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                            <p>
                                <span>TOTAL</span>
                                <Grid className="line"></Grid>
                                <span>$0,00</span>
                            </p>
                        </Grid>
                    </Grid>}
                </Grid>
            </Grid>
        );
    }
}

const mapStateToProps = store => ({
    coins: store.wallets.coins,
    transaction: store.transactions.transactions
});

export default connect(mapStateToProps)(BuySell);

