export default (state = initialState, action) => {
  switch (action.type) {
    case MARKETS_SUCCESS:
      const {payload}=action;
      const newCoins = {...state.coins};

      Object.keys(newCoins).forEach(coin => {
        payload.forEach(item => {
          if (newCoins[coin].shortName === (item.from).toUpperCase()){
            newCoins[coin].currenciesRate = payload;
            return
          }
        })
      });
      return {
        ...state,
        coins: newCoins
      };
    case MARKETS_FAILURE:
      return {
        ...state,
      };
    case GETCOINS_SUCCESS:
      const newPrice = {...state.coins};
      Object.keys(newPrice).forEach(coin => {
        action.payload.forEach(item => {
          if (newPrice[coin].shortName === (item.from).toUpperCase() && item.to ==='USD'){
            newPrice[coin].price = item.result;
            return
          }
        })
      });
      return {
        ...state,
        coins:newPrice
      };
    default:
      return state
  }
}

