import React from 'react';
import './NewsDashBoard.scss';
import {Card, CardContent, CardMedia, Typography} from "@material-ui/core";
import Image from '../NewsImage/BitmapDashboard.png'


const NewsDashBoard = ({dashBoard}) => {
  return (
    <Card className="NewsDashBoard">
      <CardMedia
        className="NewsDashBoardImage"
        image={Image}
      />
      <CardContent className="NewsDashBoardContent">
        <Typography
          gutterBottom variant="h5" component="h2"
          className="NewsDashBoardTittle">
          {dashBoard.tittle}
        </Typography>
        {dashBoard.text.map((item, key) => {
          return <Typography key={key} className="NewsDashBoardText">
            {item}
          </Typography>
        })}
      </CardContent>
    </Card>

  )
};
export default NewsDashBoard
