import * as React from "react";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper/Paper';
import Chart from '../../../../components/Chart/Chart';
import PropTypes from 'prop-types';

import Icon from "../../../transactions/components/Icon/Icons";

const defaultProps = {
  currency: '',
  fullName: '',
  icon: '',
  amount: null,
  percentage: '',
  shortName: '',
  markets: []
};

const propTypes = {
  fullName: PropTypes.string,
  currency: PropTypes.string,
  icon: PropTypes.string,
  amount: PropTypes.number,
  percentage: PropTypes.string,
  shortName: PropTypes.string,
  markets: PropTypes.array
};

class MarketCalculator extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {fullName, shortName, currency, amount, percentage, markets} = this.props;

    return (
      <Grid className="marketCalculator">
        <Paper className="marketItem">
          <Grid container
                className="firstRow"
                direction="row"
                justify="space-between"
                alignItems="center">
            <Grid container
                  className="rowItem"
                  direction="row"
                  justify="flex-start"
                  alignItems="center">
              <span className="fullName">{fullName}</span>
            </Grid>
            <Grid container
                  className="rowItem"
                  direction="row"
                  justify="flex-end"
                  alignItems="center">
              <Icon iconName={shortName}/>
            </Grid>
          </Grid>
          <Grid className="chartMarket">
            <Chart markets={markets} style={{width: "100%", height: "100px"}}/>
          </Grid>
          <Grid container
                className="datas"
                direction="row"
                justify="space-between"
                alignItems="flex-end">
            <Grid container
                  direction="column"
                  justify="flex-start"
                  alignItems="flex-start">
              <span className="percentage">{percentage}</span>
            </Grid>
            <Grid container
                  direction="column"
                  justify="flex-end"
                  alignItems="flex-end">
              <span className="currency">{currency}</span>
              <span className="amount">{amount}</span>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    );
  }
}

export default MarketCalculator;
