import React from 'react';
import {Link} from 'react-router-dom';
import './index.scss';
import KeyboardArrowRight from '@material-ui/core/es/internal/svg-icons/KeyboardArrowRight';
import KeyboardArrowLeft from '@material-ui/core/es/internal/svg-icons/KeyboardArrowLeft';
import Grid from '@material-ui/core/Grid';
import DatePickerComponent from '../../../../../components/DatePickerComponent/DatePickerComponent';


const headerMonth = props => (
  <header className="month-header">
    <Grid container
          justify="space-between"
          className="selectPeriod"
          alignItems="center">
      <Grid container
            alignItems="center">
        <h1>{props.curMonth.name}</h1>
        <Link className="Icon"
              to={'/' + props.prevMonth.slug}>
          <KeyboardArrowLeft/>
        </Link>
        <Link className="Icon"
              to={'/' + props.nextMonth.slug}>
          <KeyboardArrowRight/>
        </Link>
      </Grid>
      <Grid container
            justify="flex-end"
            direction="row"
            alignItems="center">
        <DatePickerComponent/>
      </Grid>
    </Grid>
  </header>
);

export default headerMonth;
