import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Paper from '@material-ui/core/Paper/Paper';
import Input from '@material-ui/core/Input';
import Button from '@material-ui/core/Button';
import {connect} from 'react-redux';
import {sentCoin} from "../../../../actions/actions";
import {bindActionCreators} from "redux";
import moment from "moment";

class SendCoin extends React.Component {

  state = {
    shortName: 'BTC',
    count: '',
    address: '',
    date: moment(),
    status: true,
    sender: true,
    receiver: false
  };

  handleChange = event => {
    this.setState({[event.target.name]: event.target.value});
  };

  clearState = () => {
    this.setState({coin: '', count: '', address: ''})
  };

  sentCoin = () => {
    this.props.sentCoin({
      shortName: this.state.shortName,
      count: this.state.count,
      address: this.state.address,
      date: this.state.date,
      status: this.state.status,
      sender: this.state.sender,
      receiver: this.state.receiver
    }, this.clearState());
  };

  render() {
    const {coins} = this.props;
    return (
      <Grid className="sendCoin">
        <Paper className="wrapper">
          <Grid>
            <Grid container
                  className="firstRow"
                  direction="row"
                  wrap="nowrap"
                  alignItems="center">
              <span>wallet</span>
              <FormControl className="selectName">
                <Select
                  value={this.state.shortName}
                  onChange={this.handleChange}
                  inputProps={{
                    name: 'shortName'
                  }}>
                  {Object.keys(coins).map((key, id) => {
                    const item = coins[key];
                    return <MenuItem className="labelName" key={id} value={item.shortName}>
                      {item.fullName}
                    </MenuItem>
                  })}
                </Select>
              </FormControl>
              <span>amount</span>
              <Input
                type="number"
                name="count"
                className="amount"
                value={this.state.count}
                onChange={this.handleChange}/>
            </Grid>
            <Grid>
              <span>send to</span>
              <Input type="text"
                     name="address"
                     className="sentTo"
                     fullWidth={true}
                     onChange={this.handleChange}/>
            </Grid>
            <Grid container
                  className="btnContainer"
                  direction="row"
                  justify="flex-end">
              <Button variant="outlined"
                      className="sendBtn"
                      value="submit"
                      onClick={this.sentCoin}
              >
                send
              </Button>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  coins: store.wallets.coins,
  transaction: store.transactions.transactions
});


const mapDispatchToProps = dispatch => bindActionCreators({
  sentCoin
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(SendCoin);
