import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import ChartWrapperMain from '../ChartWrappperMain/ChartWrapperMain';
import DatePickerComponent from '../../../../components/DatePickerComponent/DatePickerComponent';
import TransactionWrapper from '../../../transactions/components/TransactionWrapper/TransactionWrapper';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faChevronLeft} from '@fortawesome/free-solid-svg-icons';
import Button from '@material-ui/core/Button/Button';
import {Link, Route} from 'react-router-dom';
import Balance from '../../../dashboard/components/Balance/Balance';
import Wallets from '../../../../containers/wallets';
import connect from "react-redux/es/connect/connect";
import Icon from "../../../transactions/components/Icon/Icons";

class WalletInfo extends React.Component {
  render() {
    const {match: {params}, coins} = this.props;

    const walletType = params.type;
    const wallet = coins[walletType];

    const {shortName, fullName} = wallet;
    const currentPrice = wallet.markets[wallet.markets.length - 1].price;

    return (
      <Grid container
            justify="flex-start"
            direction="column">
        <Grid container
              className="toPreviousPage"
              direction="row"
              alignItems="center">
          <Grid>
            <Link to="/wallets">
              <Button variant="contained" className="backBtn">
                <FontAwesomeIcon className='iconInfo' icon={faChevronLeft}/>
                Back
              </Button>
            </Link>
          </Grid>
          <Grid container
                direction="row"
                alignItems="center"
                style={{width: 'auto'}}>
            <span className="title">My {fullName} Wallet</span>
            <Icon iconName={shortName}/>
          </Grid>
        </Grid>
        <Grid container
              className="walletInfo"
              wrap="wrap"
              direction="row">
          <Grid container
                className="transactionOfWallet"
                direction="column">
            <Balance wallet={wallet}/>
            <TransactionWrapper filter={shortName} wallet={wallet} small={true}/>
          </Grid>
          <Grid container
                className="chartColumn"
                direction="column">
            <Grid className="chartInfo">
              <DatePickerComponent className="datePicker"/>
              <Grid container
                    alignItems="center"
                    className="data"
                    justify="space-between"
                    direction="row">
                <Grid className="fromTo">
                  <span>{wallet.shortName}</span>
                  <span>/</span>
                  <span>USD</span>
                </Grid>
                <Grid>
                  <span className="percentage">-1.14%</span>
                  <span className="price">{currentPrice}</span>
                  <span className="currency">USD</span>
                </Grid>
              </Grid>
              <ChartWrapperMain wallet={wallet}/>
            </Grid>
          </Grid>
        </Grid>
        <Route exact path="/wallets" component={Wallets}/>
      </Grid>
    );
  }
}


const mapStateToProps = store => ({
  coins: store.wallets.coins
});

export default connect(mapStateToProps)(WalletInfo);
