import * as React from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Chart from '../../../../components/Chart/Chart';
import Transaction from '../../../transactions/components/Transaction/Transaction';
import {faLongArrowAltLeft} from '@fortawesome/free-solid-svg-icons';
import Icon from "../../../transactions/components/Icon/Icons";

const defaultProps = {
  shortName: '',
  fullName: '',
  imageName: '',
  count: "0"
};


const propTypes = {
  shortName: PropTypes.string,
  fullName: PropTypes.string,
  imageName: PropTypes.string,
  count: PropTypes.string.isRequired
};


class WalletDetail extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {shortName, fullName,  count, markets} = this.props;
    return (
      <Grid container
            className="walletDetail">
        <Paper className="walletItem">
          <Grid container
                direction="column">
            <Grid container
                  direction="row"
                  wrap="nowrap"
                  alignItems="center">
              <Grid container
                    direction="row"
                    wrap="nowrap"
                    alignItems="center">
                <Icon iconName={shortName}/>
                <span className="fullName">{fullName}</span>
              </Grid>
              <Grid container
                    direction="column"
                    alignItems="flex-end">
                <span className="shortName currency">{shortName}</span>
                <span className="amount">{count}</span>
              </Grid>
            </Grid>
            <Grid className="chartWallet">
              <Chart markets={markets}/>
            </Grid>
            <Grid className="transactionWallet">
              <Transaction imageName="ETH.png" shortName="ETH" direction={faLongArrowAltLeft}
                           time='16:23' date="12 dec 2018" small={true}
                           address="1PRj85hu9RXPZTzxtko9stfs6nRo1vyrQB" status={true}
                           count={"0.009"}/>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    );
  }
}

export default WalletDetail;
