import {Button, Grid, withStyles} from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import React from "react";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faChevronRight, faEllipsisV, faUpload, faUser} from '@fortawesome/free-solid-svg-icons';
import {styles} from "../style";
import "./style.scss";
import SettingsList from '../SettingsList/SettingsList';

class SettingAvatar extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      drobdown: false
    };

    this.showDrobDown = this.showDrobDown.bind(this);
    this.hideDrobDown = this.hideDrobDown.bind(this);
  }

  showDrobDown() {
    this.setState({drobdown: true});
  };

  hideDrobDown() {
    this.setState({drobdown: false});
  };


  render() {
    return (
      <Grid container className="avatarBlock">
        <FontAwesomeIcon className='iconMore' icon={faEllipsisV} onClick={this.showDrobDown}/>
        {this.state.drobdown === true && <Grid style={{color: '#fff'}}>
            <span className='close'>
                <FontAwesomeIcon className='closeSubmenu' icon={faChevronRight} onClick={this.hideDrobDown}/>
            </span>
          <SettingsList/>
        </Grid>}

        <Grid item xs={5}>
          <Avatar className="avatar">
            <FontAwesomeIcon size="3x" className='icons' icon={faUser}/>
          </Avatar>
        </Grid>
        <Grid className="block" item xs={7} container>
          <Grid alignItems="center" container justify="space-between" style={{paddingBottom: '30px'}}>
            <Grid className="label">
              <h3>Change Picture</h3>
              <p>Max file is 10Mb</p>
            </Grid>
            <Grid>
              <input
                accept="image/*"
                className="uploadInput"
                id="contained-button-file"
                multiple
                type="file"
              />
              <label htmlFor="contained-button-file">
                <Button className='btnSave' variant="contained" component="span">
                  <FontAwesomeIcon className='iconInBtn'
                                   icon={faUpload}/>
                  Upload
                </Button>
              </label>
            </Grid>
          </Grid>
          <Grid alignItems="center" justify='space-between' container>
            <Grid className="label" item xs={6}>
              <h3>Change Password</h3>
              <p>Enable 2-factor authentication
                on the <span style={{color: '#B158E2'}}>security page</span></p>
            </Grid>
            <Button className='btnSave' variant="contained">Change Password</Button>
          </Grid>
        </Grid>
      </Grid>
    )
  }
}


export default withStyles(styles)(SettingAvatar);
