import {MARKETS_REQUEST,MARKETS_SUCCESS,MARKETS_FAILURE} from '../constants/markets';
import { toast } from "react-toastify";
import Config from "../config";


export function getMarkets(currency) {
    return (dispatch) => {
        dispatch({
            type: MARKETS_REQUEST,
        });
        fetch(`${Config.serverUrl}/v1/currencies/values/${currency}`, {
        })
            .then(res => res.json())
            .then(json=>{
                dispatch({
                    type:MARKETS_SUCCESS,
                    payload:json.data
                });
            })
            .catch(json => {
                toast.error('Server error');
                dispatch({
                    type: MARKETS_FAILURE,
                    payload: json.message
                })
            });
    };
}
