import * as React from "react";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import {Link} from 'react-router-dom';


import Medium from '../../../../images/medium-with-circle.png';
import Shape1 from '../../../../images/Shape1.png';
import Twitter from '../../../../images/twitter-with-circle.png';
import Insta from '../../../../images/instagram-with-circle.png';
import Linkedin from '../../../../images/linkedin-with-circle.png';
import Shape from '../../../../images/Shape.png';

class SingUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      subMenu: false,
      username: {
        value: '',
      },
      email: {
        value: ''
      },
      password: {
        value: '',
      },
      phone: {
        value: ''
      },
      firstname: {
        value: ''
      }
    };

    this.showSubMenu = this.showSubMenu.bind(this);
  }

  handleInputChange = (event) => {
    event.persist();
    const {name, value} = event.target;
    this.setState(prevState => ({
      [name]: {
        ...prevState[name],
        value,
      },
    }));
  };

  showSubMenu() {
    const currentState = this.state.subMenu;
    this.setState({subMenu: !currentState});
  };

  handleSubmit = (event) => {
    event.preventDefault();
    const {username, password, email,firstname,phone} = this.state;
    this.props.onSubmit(email.value, username.value, password.value,firstname.value,phone.value,);
  };


  render() {
    const {username, password, email,firstname,phone} = this.state;

    return (
      <Grid className="background-wrapper">
        <Grid className="top">
          <Grid className="photo"></Grid>
          <Grid className="supp">
            <Grid className="formlang">
              <select className="lang">
                <option value="En">
                  EN
                </option>
                <option value="Fr">
                  FR
                </option>
                <option value="Ru">
                  RU
                </option>
              </select>
            </Grid>
            <p><a href="" className="su">SUPPORT</a></p>
          </Grid>
          <Grid className="sim"
                onClick={this.showSubMenu}></Grid>
          {this.state.subMenu === true &&
          <Grid className="supmob">
            <a href=""
               className="su1">SUPPORT</a>
            <Grid className="formlang">
              <select className="lang">
                <option value="En">
                  EN
                </option>
                <option value="Fr">
                  FR
                </option>
                <option value="Ru">
                  RU
                </option>
              </select>
            </Grid>
          </Grid>}
        </Grid>

        <Grid className="login login-sing" style={{maxWidth: "338px"}}>
          <p><span className="log">Sign Up</span></p>
          <form id="form"
                onSubmit={this.handleSubmit}
                className="form-sing"
                direction="column">
            <Grid container
                  className="bla"
                  direction="row"
                  wrap="nowrap"
                  justify="space-between">
              <input value={username.value}
                     name="username"
                     onChange={this.handleInputChange}
                     type="text" placeholder="Name"
                     required className="nem"/>
              <input value={firstname.value}
                     name="firstname"
                     onChange={this.handleInputChange}
                     type="text" placeholder="First Name" required className="ema"/>

            </Grid>
            <Grid style={{width: "91%"}}>
              <input value={phone.value}
                     name="phone"
                     onChange={this.handleInputChange}
                     type="tel" placeholder="Mobile Phone" required className="pa"/>
            </Grid>
            <Grid style={{width: "91%"}}>
              <input value={email.value}
                     name="email"
                     onChange={this.handleInputChange}
                     type="email" placeholder="Email" required className="pa"/>
            </Grid>
            <Grid style={{width: "91%"}}>
              <input value={password.value}
                     name="password"
                     onChange={this.handleInputChange}
                     type="password" placeholder="Choose a Password" required className="pa"/>
            </Grid>
            <Grid>
              <button type="submit"><span className="butcol">Create account</span></button>
            </Grid>
          </form>
          <p className="sig">
            <span className="sign">
              Already have an account?
                <Link to='/login' style={{ textDecoration: 'none'}}>
                  <a href="#" className="up" style={{ textDecoration: 'none'}}>
                  Log in
                </a>
                </Link>
            </span>
          </p>
        </Grid>
        <Grid className="bottom">
          <Grid className="xz">
            <p><a href=""><img src={Medium}/></a></p>
            <p><a href=""><img src={Shape1}/></a></p>
            <p><a href=""><img src={Twitter}/></a></p>
            <p><a href=""><img src={Insta}/></a></p>
            <p><a href=""><img src={Linkedin}/></a></p>
            <p><a href=""><img src={Shape}/></a></p>
          </Grid>
          <Grid className="contact">
            <p className="consh">
              <a href="" className="terms">Terms of service</a>
              <a href="" className="terms">Contact</a>
              <a href="" className="terms">Support</a>
            </p>
          </Grid>
        </Grid>
        <Grid></Grid>
      </Grid>
    );
  }
}


export default SingUp;
