import React from 'react';
import {Button, Grid, Input} from "@material-ui/core";
import "./style.scss";
import connect from 'react-redux/es/connect/connect';
import {bindActionCreators} from 'redux';
import {onChangePersonalDetails} from "../../../../../modules/action"
class SettingsName extends React.Component {

  onChange = (event) => {
    this.props.onChangePersonalDetails({
      [event.target.name]: event.target.value,
    })
  };

  render() {
    const {user} = this.props;
    return (
      <Grid container justify="space-between">
        <Grid container alignItems="flex-end" justify='space-between'>
          <Grid item xs={5} className="nikeNames">
            <h3>Nick Name</h3>
            <p>This name will be part of your public profile.</p>
          </Grid>
          <Grid item xs={7} className="input">
            <Input
              fullWidth
              type="text"
              onChange={this.onChange}
              name="username"
              value={user.username}
              className="inputs"/>
          </Grid>
        </Grid>

        <Grid container alignItems="flex-end" justify='space-between' style={{paddingTop: '15px'}}>
          <Grid item xs={5} container direction="row" className="email">
            <h3>Email</h3>
          </Grid>
          <Grid item xs={7} className="input">
            <Input
              fullWidth
              type="email"
              onChange={this.onChange}
              name="email"
              value={user.email}
              className="inputs"/>
          </Grid>
          <Grid container justify='flex-end'>
            <Button className='btnSave' variant="contained">Save</Button>
          </Grid>
        </Grid>
      </Grid>
    )
  }
}

const mapStateToProps = store => ({
  user: store.user.settings
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onChangePersonalDetails
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(SettingsName);

