import React, {Component} from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import WireTransfert from './WireTransfert/WireTransfert';
import Deposit from './Deposit/Deposit';
import Transfer from './Transfer/Transfer';

class BankAccount extends Component {
    state = {
        complete: 1
    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };

    onSubmit=()=>{
        this.setState({complete: this.state.complete+1});
    };

    render() {
        const {coins} = this.props;
        return (
            <Grid className="bank-account">
                { this.state.complete === 1 && <WireTransfert handleChange={this.handleChange} onSubmit={this.onSubmit}  />}
                {this.state.complete === 2 && 
                <Grid>
                    <Deposit onSubmit={this.onSubmit} handleChange={this.handleChange}/>  
                </Grid>
                }
                {this.state.complete === 3 && <Grid>
                    <Transfer handleChange={this.handleChange}/>
                </Grid>}
            </Grid>
        );
    }
}

export default BankAccount;

