import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper/Paper'
import Chart from '../../../../components/Chart/Chart';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faEllipsisV} from '@fortawesome/free-solid-svg-icons';


import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";


const TIME_IN_MS = {
  ONE_MONTH: 2592000000,
  THREE_MONTH: 7776000000,
  SIX_MONTH: 15552000000,
  ONE_YEAR: 31104000000,
  ALL: 0
};

class ChartWrapperMain extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedValue: TIME_IN_MS.ONE_MONTH,
      tab: "1m",
    };
  }


  handleChange = event => {
    this.setState({selectedValue: event.target.value});
  };


  handleShow = event => {
    this.setState({tab: event.target.value});
  };




  render() {
    const {wallet: {fullName, markets}} = this.props;

    return (
      <Grid className="chart">
        <Paper className="wrapper">
          <Grid container
                alignItems="center"
                justify="space-between"
                direction="row">
            <Grid container
                  alignItems="center"
                  justify="flex-start"
                  className="headerContent"
                  direction="row">
              <Grid>
                <h1 className="title">{fullName} Chart</h1>
              </Grid>
              <Grid className="radioBtnGroup"
                    container
                    alignItems="center"
                    justify="flex-end"
                    direction="row">
                <label>
                  <input type="radio"
                         name="radio-button"
                         value={TIME_IN_MS.ONE_MONTH}
                         onChange={this.handleChange}
                         aria-label="1m"
                         checked={this.state.selectedValue === TIME_IN_MS.ONE_MONTH}/>
                  <span>1m</span>
                </label>
                <label>
                  <input type="radio"
                         name="radio-button"
                         value={TIME_IN_MS.THREE_MONTH}
                         onChange={this.handleChange}
                         aria-label="3m"
                         checked={this.state.selectedValue === TIME_IN_MS.THREE_MONTH}/>
                  <span>3m</span>
                </label>
                <label>
                  <input type="radio"
                         name="radio-button"
                         value={TIME_IN_MS.SIX_MONTH}
                         onChange={this.handleChange}
                         aria-label="6m"
                         checked={this.state.selectedValue === TIME_IN_MS.SIX_MONTH}/>
                  <span>6m</span>
                </label>
                <label>
                  <input type="radio"
                         name="radio-button"
                         value={TIME_IN_MS.ONE_YEAR}
                         onChange={this.handleChange}
                         aria-label="1y"
                         checked={this.state.selectedValue === TIME_IN_MS.ONE_YEAR}/>
                  <span>1y</span>
                </label>
                <label>
                  <input type="radio"
                         name="radio-button"
                         value={TIME_IN_MS.ALL}
                         onChange={this.handleChange}
                         aria-label="All"
                         checked={this.state.selectedValue === TIME_IN_MS.ALL}/>
                  <span>All</span>
                </label>
              </Grid>
              <Grid className="radioBtnGroupMob">
                <FormControl className="tabsBlock">
                  <Select className="selectTab"
                          value={this.state.tab}
                          onChange={this.handleShow}
                          inputProps={{
                            select: '1m',
                          }}>
                    <MenuItem value={TIME_IN_MS.ONE_MONTH}>1m</MenuItem>
                    <MenuItem value={TIME_IN_MS.THREE_MONTH}>3m</MenuItem>
                    <MenuItem value={TIME_IN_MS.SIX_MONTH}>6m</MenuItem>
                    <MenuItem value={TIME_IN_MS.ONE_YEAR}>1y</MenuItem>
                    <MenuItem value={TIME_IN_MS.ALL}>All</MenuItem>
                  </Select>
                  <FontAwesomeIcon className='iconInfo' icon={faEllipsisV}/>
                </FormControl>
              </Grid>
            </Grid>
            <Chart markets={markets} style={{width: "100%", height: "200px"}}/>
          </Grid>
        </Paper>
      </Grid>
    );
  }
}

export default ChartWrapperMain;
