import React from "react";
import "./index.css";
import {Typography} from "@material-ui/core";


class Day extends React.Component {


  render() {

    const cssClasses = this.props.firstDayIndex
      ? `day first-index-${this.props.firstDayIndex}`
      : "day";

    return (
      <article className={cssClasses}>
        <div className="borderBox">
          <header>
            {this.props.day}
          </header>
          <Typography className="dayCity">{this.props.events.city}<br/>{this.props.events.country}</Typography>
        </div>
        <Typography className="dayAddress">{this.props.events.name}</Typography>
      </article>
    );
  }
}


export default Day;
