import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import {Link, Redirect} from 'react-router-dom';
import Medium from '../../../../images/medium-with-circle.png';
import Shape1 from '../../../../images/Shape1.png';
import Twitter from '../../../../images/twitter-with-circle.png';
import Insta from '../../../../images/instagram-with-circle.png';
import Linkedin from '../../../../images/linkedin-with-circle.png';
import Shape from '../../../../images/Shape.png';

class LogIn extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      subMenu: false,
      email: {
        value: '',
      },
      password: {
        value: '',
      },
    };

    this.showSubMenu = this.showSubMenu.bind(this);
  }

  handleInputChange = (event) => {
    event.persist();
    const {name, value} = event.target;

    this.setState(prevState => ({
      [name]: {
        ...prevState[name],
        value,
      },
    }));
  };

  showSubMenu() {
    const currentState = this.state.subMenu;
    this.setState({subMenu: !currentState});
  };


  handleSubmit = (event) => {
    event.preventDefault();

    const {email, password} = this.state;

    this.props.onSubmit(email.value, password.value);
    return <Redirect to='/main'/>
  };


  render() {

    return (
      <Grid className="background-wrapper">
        <Grid className="top">
          <Grid className="photo"></Grid>
          <Grid className="supp">
            <Grid className="formlang">
              <select className="lang">
                <option value="En">
                  EN
                </option>
                <option value="Fr">
                  FR
                </option>
                <option value="Ru">
                  RU
                </option>
              </select>
            </Grid>
            <p><a href=""
                  className="su">SUPPORT</a></p>
          </Grid>
          <Grid className="sim"
                onClick={this.showSubMenu}></Grid>
          {this.state.subMenu === true &&
          <Grid className="supmob">
            <a href=""
               className="su1">SUPPORT</a>
            <Grid className="formlang">
              <select className="lang">
                <option value="En">
                  EN
                </option>
                <option value="Fr">
                  FR
                </option>
                <option value="Ru">
                  RU
                </option>
              </select>
            </Grid>
          </Grid>}
        </Grid>

        <Grid className="login">
          <p><span className="log">Log In</span></p>
          <form onSubmit={this.handleSubmit} id="form">
            <input type="text"
                   placeholder="Email"
                   required
                   name="email"
                   value={this.state.email.value}
                   onChange={this.handleInputChange}
            />
            <input type="password"
                   placeholder="Password"
                   required
                   name="password"
                   value={this.state.password.value}
                   onChange={this.handleInputChange}
            />
            <button type="submit"><span className="butcol">Log In</span></button>
          </form>
          <p className="sig">
                        <span className="sign">Forgot password? Don't have an account
                            <Link to='/signup' style={{ textDecoration: 'none'}} className="up">Sign Up</Link>
                        </span>
          </p>
        </Grid>
        <Grid className="bottom">
          <Grid className="xz">
            <p><a href=""><img alt='icon' src={Medium}/></a></p>
            <p><a href=""><img alt='icon' src={Shape1}/></a></p>
            <p><a href=""><img alt='icon' src={Twitter}/></a></p>
            <p><a href=""><img alt='icon' src={Insta}/></a></p>
            <p><a href=""><img alt='icon' src={Linkedin}/></a></p>
            <p><a href=""><img alt='icon' src={Shape}/></a></p>
          </Grid>
          <Grid className="contact">
            <p className="consh">
              <a href=""
                 className="terms">Terms of service</a>
              <a href=""
                 className="terms">Contact</a>
              <a href=""
                 className="terms">Support</a>
            </p>
          </Grid>
        </Grid>
        <Grid></Grid>
      </Grid>
    );
  }
}


export default LogIn;
