import * as React from "react";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import Market from '../Market/Market';
import PropTypes from 'prop-types';
import {Link, Route} from 'react-router-dom';
import Tranding from '../Tranding/Tranding';

const defaultProps = {
    small: false
};


const propTypes = {
    small: PropTypes.bool,
};

class MarketList extends React.Component {
    static defaultProps = defaultProps;
    static propTypes = propTypes;

    render() {
        const {small, coins, currentValue} = this.props;
        let currentCoin = null;
        Object.keys(coins).forEach((key) => {
            if ((coins[key].shortName).toLowerCase() === currentValue) {
                currentCoin = coins[key];
            }
        });
        return (
            <Grid className="marketList">
                <Grid container
                      direction="row"
                      alignItems="center">
                    <Grid container
                          className="marketItems"
                          justify="flex-start">
                        { (small===true) ?
                            currentCoin.currenciesRate.map((item, id) => {
                                return (
                                    <Link to={`/trading/${item.from}`} className="linkToTranding" key={id}>
                                        <Market
                                            currency={item.from} count={item.result}
                                            shortName={item.to} amount={item.amount}
                                            small={small} markets={currentCoin.markets} percentage="1.14%"
                                        />
                                    </Link>)
                            }) :
                            Object.keys(coins).map((key,id)=>{
                                const item = coins[key];
                                return(
                                    <Link to={`/trading/${item.shortName}`} className="linkToTranding" key={id}>
                                        <Market
                                            currency={item.shortName} count={item.count}
                                            shortName={item.shortName} amount={item.amount}
                                            markets={item.markets} percentage="1.14%"
                                        />
                                    </Link>
                                )
                            })
                        }

                    </Grid>
                </Grid>
                <Route exact path="/trading" component={Tranding}/>
            </Grid>
        );
    }
}

export default MarketList;
