import React from 'react';
import './style.scss';
import PropTypes from "prop-types";
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper/Paper';
import Chart from '../../../../components/Chart/Chart';

const defaultProps = {
  shortName: '',
  currency: '',
  coint: '',
  amount: 0,
  percentage: '',
  small: false,
  markets: []
};

const propTypes = {
  shortName: PropTypes.string,
  currency: PropTypes.string,
  coint: PropTypes.string,
  amount: PropTypes.number,
  percentage: PropTypes.string,
  small: PropTypes.bool,
  markets: PropTypes.array
};

class Market extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {shortName, currency, amount, percentage, small, markets, count} = this.props;
    const price = (1/count).toFixed(4);
    return (
      <div className="market">
        {small === false && <Paper className="marketItem">
          <Grid container
                className="firstRow"
                direction="row"
                justify="space-between"
                alignItems="center">
            <Grid className="fromTo">
              <span>{shortName}</span>
              <span>/</span>
              <span>USD</span>
            </Grid>
            <Grid container
                  className="rowItem"
                  direction="row"
                  justify="flex-end"
                  alignItems="center">
              <span className="coint">{count}</span>
              <span className="currency">{currency}</span>
            </Grid>
          </Grid>
          <Grid className="chartMarket">
            <Chart markets={markets} style={{width: "100%", height: "100px"}}/>
          </Grid>
          <Grid container
                className="volume"
                direction="row"
                justify="space-between"
                alignItems="flex-end">
            <Grid>
              <span>Volume:</span>
              <span className="amount">{amount}</span>
              <span className="shortName">{shortName}</span>
            </Grid>
            <Grid>
              <span className="percentage">{percentage}</span>
            </Grid>
          </Grid>
        </Paper>}
        {small === true && <Grid className="marketItemSmall"
                                 container
                                 alignItems="center"
                                 direction="row">
          <Grid container
                className="leftBlock"
                direction="row"
                alignItems="center"
                justify="space-between">
            <Grid container
                  className="firstRow"
                  direction="row"
                  justify="space-between"
                  alignItems="center">
              <Grid className="fromTo">
                <span>{shortName}</span>
                <span>/</span>
                <span>{currency}</span>
              </Grid>
              <Grid>
                <span className="percentage">{percentage}</span>
              </Grid>
            </Grid>
            <Grid container
                  className="volume"
                  direction="row"
                  justify="space-between"
                  alignItems="flex-end">
              <Grid container
                    className="rowItem"
                    direction="row"
                    justify="flex-start"
                    alignItems="center">
                <span className="coint">{price}</span>
                <span className="currency">{currency}</span>
              </Grid>
            </Grid>
          </Grid>

          <Grid className="chartMarket rightBlock">
            <Chart markets={markets}/>
          </Grid>
        </Grid>}
      </div>
    );
  }
}


export default Market;
