import React from 'react';
import './CalculatorMain.scss';
import {Grid, TextField, withStyles} from "@material-ui/core";
import Converter from './Converter'
import CalculatorButtons from './CalculatorButtons';
import {connect} from "react-redux";
import {getCoins} from "../../../../actions/getcoins";
import {bindActionCreators} from "redux";

const styles = () => ({

  input: {
    marginLeft: 10,
    fontWeight: 300,
    fontSize: 40,
    color: "#06062B"
  },
  result: {
    fontWeight: 300,
    fontSize: 30,
    textAlign: "center",
    color: " #06062B"
  }

});


class CalculatorMain extends React.Component {


  state = {
    currentCryptoValue: 'bitcoin',
    currentCurrency: 'bitcoin',
    currentValue: '1',
    exchange: false,
    value: ''
  };

  result = () => {
    this.setState({value: parseFloat((this.props.coins[this.state.currentCryptoValue].price / this.props.coins[this.state.currentCurrency].price) * this.state.currentValue).toFixed(2)})
  };

  inverse = () => {
    this.setState({currentCurrency: this.state.currentCryptoValue});
    this.setState({currentCryptoValue: this.state.currentCurrency});
    this.setState({value: ''});
    this.setState({currentValue: ''})
  };

  onChangeValue = event => {
    return () => {
      this.setState({currentValue: this.state.currentValue + event}, this.result);
    };
  };

  componentDidMount() {
    Object.keys(this.props.coins).map((key)=>{
      const item = this.props.coins[key];
      this.props.getCoins(item.shortName);
    })
  }

  clearCurrentValue = () => {
    this.setState({currentValue: ''}, () => {
      this.setState({value: ''})
    });
  };

  handleChange = name => event => {
    this.setState({[name]: event.target.value}, this.result);
  };


  render() {
    const {classes, coins} = this.props;
    const {currentValue, value, currentCryptoValue, currentCurrency} = this.state;
    const res = currentValue + " " + coins[currentCryptoValue].shortName + " = " + value + " " + coins[currentCurrency].shortName;
    return (
      <Grid container
            direction="row"
            item xs={5}
            alignItems="flex-start">
        <Grid className="CalculatorMain" item xs={12}>
          <Converter
            coins={coins}
            inverse={this.inverse}
            handleChange={this.handleChange}
            data={this.state}
          />
          <TextField
            inputProps={{
              className: classes.input
            }}
            variant="outlined"
            className="textField"
            type="number"
            value={currentValue}
            onChange={this.handleChange('currentValue')}
            margin="normal">
          </TextField>
          <CalculatorButtons
            clearCurrentValue={this.clearCurrentValue}
            onChangeValue={this.onChangeValue}
          />
          <TextField
            variant="outlined"
            className="textField"
            inputProps={{
              className: classes.result
            }}
            margin="normal"
            type="string"
            value={res}
          >
          </TextField>
        </Grid>
        <Grid item xs={6}>

        </Grid>
      </Grid>
    )
  }
}

const mapStateToProps = store => ({
  coins: store.wallets.coins
});

const mapDispatchToProps = dispatch=> bindActionCreators({
  getCoins
},dispatch);

export default connect(mapStateToProps,mapDispatchToProps)(withStyles(styles)(CalculatorMain))
