import {push} from 'connected-react-router'
import * as types from '../constants/auth';
import {LOGIN_FAILURE, LOGIN_SUCCESS, SIGNUP_FAILURE} from '../constants/auth';
import { toast } from "react-toastify";
import Config from "../config";


export function signup(email, username, password,firstname,phone) {
  return (dispatch) => {
    dispatch({
      type: types.SIGNUP_REQUEST,
    });

    fetch(`${Config.serverUrl}/v1/user/signUp`, {
      method: "POST",
      body: JSON.stringify({
        email,
        firstname,
        phone,
        username,
        password
      }),
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
    })
    .then(res => res.json())
    .then(data => {
      if (data) {
        return data
      }
      throw(data)
    })
    .then(json => {
      toast.success('Successfully registered. Please login.');
      dispatch(push('/login'));
    })
    .catch(data => {
      toast.error('Server error');
      dispatch({
        type: SIGNUP_FAILURE,
        payload: data
      })
    });
  };
}

export function login(email, password) {
  return (dispatch) => {
    dispatch({
      type: types.LOGIN_REQUEST,
    });

    fetch(`${Config.serverUrl}/v1/user/signIn`, {
      method: "POST",
      body: JSON.stringify({
        email,
        password
      }),
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      }
    })
    .then(res => res.json())
    .then(response => {
      const { access_token } = response;
      if (!access_token) {
        throw(response)
      }

      localStorage.setItem("token", access_token);
      dispatch({
        type: LOGIN_SUCCESS,
        payload: response
      })
    })
    .catch(data => {
      toast.error('Server error');
      dispatch({
        type: LOGIN_FAILURE,
        payload: data
      })
    });
  };
}

export function logout() {
  return (dispatch) => {
    dispatch({
      type: types.LOGOUT_REQUEST,
    });

    fetch(`${Config.serverUrl}/v1/user/logout`, {
      method: "POST",
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
        'access_token': localStorage.getItem('token')
      },
    })
    .then((res) => res.json())
    .then(json => {
      if (json.success) {
        dispatch({
          type: types.LOGOUT_SUCCESS,
          payload: json,
        });
        dispatch(push('/login'));
        return;
      }
      throw(json)
    })
    .catch(json => {
      toast.error('Server error');
      dispatch({
        type: types.LOGOUT_FAILURE,
        payload: json,
      })
    });
  };
}

