import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Wallet from '../Wallet/Wallet';
import connect from 'react-redux/es/connect/connect';

class WalletItems extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selected: 0
    };
  }


  handleClick = (id) => {
    this.setState({
      selected: id
    })
  };

  render() {
    const {wallets} = this.props;
    return (
      <Grid className="walletList">
        <h1 className="title">My Wallets</h1>
        <Grid container
              direction="row"
              justify="flex-start"
              alignItems="center">
          {Object.keys(wallets.coins).slice(0, 4).map((key, id) => {
            const item = wallets.coins[key];
            return <Grid className="walletItem"
                         key={id}>
              <Wallet shortName={item.shortName}
                      price={item.price}
                      key={id}
                      active={id === this.state.selected}
                      imageName={item.imageName}
                      fullName={item.fullName}
                      onClick={(e) => this.handleClick(id)}/>
            </Grid>
          })}
        </Grid>
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  wallets: store.wallets
});

export default connect(mapStateToProps)(WalletItems);
