import * as React from "react";
import PropTypes from "prop-types";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import connect from 'react-redux/es/connect/connect';
import Button from '@material-ui/core/Button/Button';
import {Link} from 'react-router-dom'

const defaultProps = {
  amount: 0,
  spent: 0,
  recived: 0,
  status: 0,
  transaction: 0
};


const propTypes = {
  amount: PropTypes.number,
  spent: PropTypes.number,
  recived: PropTypes.number,
  status: PropTypes.number,
  transaction: PropTypes.number
};

class Overview extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {oztgData} = this.props;
    return (
      <Grid container
            className="overview"
            direction="column">
        <h1 className="title">Overview</h1>
        <Paper className="wrapper">
          <Grid container
                className="statistic"
                direction="row">
            <Grid container
                  className="transactions"
                  direction="column"
                  justify='center'
                  alignItems="center">
              <span className="count">{oztgData.transactions}</span>
              <span className="countOf">Transaction</span>
            </Grid>
            <Grid container
                  className="status"
                  direction="column"
                  justify="center"
                  alignItems="center">
              <Grid container
                    className="blockWrapper"
                    direction="column"
                    justify="center"
                    alignItems="center">
                <span className="count">{oztgData.pending}</span>
                <span className="countOf">Wallets</span>
              </Grid>
            </Grid>
          </Grid>
          <Grid className="tabsView">
              <span>Current balance</span>
              <Grid container
                    className="tabContainer"
                    direction="row"
                    justify="flex-start"
                    alignItems="center">
                <span className="number">{oztgData.balance}</span>
                <span className="name">OZTG</span>
              </Grid>
            <Grid container
                  className="buy-block"
                  direction="row"
                  justify="space-between"
                  alignItems="center">
                <span className="number">{oztgData.count} </span>
                <span className="name"> EUR</span>
                <span className="percentage">+12%</span>
              <Link to="/main/buy-sell" className="linkDecoration">
                <Button variant="outlined"
                        className="buy-sell"
                        value="submit">
                    buy/sell
                </Button>
              </Link>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    );
  }
}


const mapStateToProps = store => ({
  oztgData: store.wallets.coins.oztg
});

export default connect(mapStateToProps)(Overview);
