import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import TransactionItems from '../TransactionItems/TransactionItems';
import Paper from '@material-ui/core/Paper/Paper';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Tabs from '@material-ui/core/Tabs/Tabs';
import Tab from '@material-ui/core/Tab/Tab';
import Typography from '@material-ui/core/Typography/Typography';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button/Button';
import DatePickerComponent from '../../../../components/DatePickerComponent/DatePickerComponent';

import connect from 'react-redux/es/connect/connect';
import {Link} from "react-router-dom";

function TabContainer(props) {
  return (
    <Typography component="div">
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

const defaultProps = {
  small: false,
};

const propTypes = {
  small: PropTypes.bool
};

class TransactionWrapper extends React.Component {
  //TODO: MAKE INTERACTIVE TRANSACTIONS

  static defaultProps = defaultProps;
  static propTypes = propTypes;
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({value});
  };

  calcTrasactionLengths = () => {
    return this.props.transactions.length;
  };

  getTransactions = () => {
    const filter = this.props.filter;
    return this.props.transactions.filter((transaction) => {
      if (this.state.value === 1) {
        return filter ? transaction.sender && transaction.shortName === filter : transaction.sender;
      }

      if (this.state.value === 2) {
        return filter ? transaction.receiver && transaction.shortName === filter : transaction.receiver;
      }

      return (filter ? transaction.shortName === filter : true);
    })
  };

  render() {
    const {small} = this.props;
    const {value} = this.state;

    return (
      <Grid className="transactionWrapper">
        {small === true &&
        <Grid>
          <h1 className="title">Transactions</h1>
          <Paper className="smallTransaction">
            <Grid className="tabsView">
              <AppBar position="static">
                <Tabs value={value}
                      onChange={this.handleChange}>
                  <Tab label="ALL"/>
                  <Tab label="SEND"/>
                  <Tab label="RECENT"/>
                </Tabs>
              </AppBar>
              {value === 0 && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
              {(value === 1) && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
              {value === 2 && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
            </Grid>
            <Grid container
                  direction="row"
                  className="btn"
                  alignItems="center"
                  justify="flex-end">
              <Button
                component={Link}
                to="/transactions"
                variant="outlined"
                className="viewBtn">
                View more
              </Button>
            </Grid>
          </Paper>
        </Grid>
        }
        {small === false && <Grid>
          <Grid className="transactionBigWrapper">
            <DatePickerComponent/>
            <Grid className="tabsView">
              <Grid container
                    alignItems='center'
                    justify='space-between'
                    direction='row'>
                <Grid className="information"
                      container
                      direction="row"
                      justify='flex-start'
                      alignItems="flex-end">
                                    <span className="countOf">
                                        <span className="count">{this.calcTrasactionLengths()}</span>
                                        Transactions
                                    </span>
                  <Button variant="contained" className="export">
                    Export History
                  </Button>
                </Grid>
                <AppBar position="static">
                  <Tabs value={value} onChange={this.handleChange}>
                    <Tab label="ALL"/>
                    <Tab label="SEND"/>
                    <Tab label="RECENT"/>
                  </Tabs>
                </AppBar>
              </Grid>
              {value === 0 && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
              {value === 1 && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
              {value === 2 && <TabContainer>
                <Grid container
                      className="tabContainer"
                      direction="row"
                      justify="flex-start"
                      alignItems="center">
                  <Grid container
                        direction="column"
                        alignItems="center">
                    <TransactionItems transactions={this.getTransactions()} small={small}/>
                  </Grid>
                </Grid>
              </TabContainer>}
            </Grid>
          </Grid>
        </Grid>}
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  transactions: store.transactions.transactions
});

export default connect(mapStateToProps)(TransactionWrapper);

