import * as types from '../constants/auth';

const token = localStorage.getItem('token');

const initialState = {
  isAuthenticated: false,
  user: null,
  token,
};


export default function auth(state = initialState, action) {

  const {
    LOGIN_SUCCESS,
    SIGNUP_FAILURE,
    LOGIN_FAILURE,
    RECIEVE_AUTH_FAILURE,
    LOGOUT_SUCCESS
  } = types;

  console.log(action);
  switch (action.type) {
    case LOGIN_SUCCESS:
      return {
        ...state,
        isAuthenticated: true,
        user: action.payload.user,
        token: action.payload.token,
      };
    case SIGNUP_FAILURE:
    case LOGIN_FAILURE:
    case RECIEVE_AUTH_FAILURE:
    case LOGOUT_SUCCESS:
      localStorage.removeItem('token');
      return {
        ...state,
        isAuthenticated: false,
        user: null,
        token: '',
      };
    default:
      return state;
  }
}
