import * as React from "react";
import PropTypes from "prop-types";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faCheck, faHistory, faLongArrowAltLeft, faLongArrowAltRight} from '@fortawesome/free-solid-svg-icons';
import Button from '@material-ui/core/Button/Button';
import Icon from "../Icon/Icons";
import {formatedDate} from "../../../../utils"


const defaultProps = {
  shortName: '',
  time: '',
  date: '',
  count: '',
  address: '',
  imageName: '',
  status: false,
  small: false,
  sender: true,
  receiver: false
};


const propTypes = {
  shortName: PropTypes.string,
  date: PropTypes.string.isRequired,
  count: PropTypes.string,
  address: PropTypes.string,
  imageName: PropTypes.string,
  status: PropTypes.bool,
  small: PropTypes.bool,
  sender: PropTypes.bool,
  receiver: PropTypes.bool,
};




class Transaction extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {shortName, date, count, address, sender, receiver, small, status} = this.props;


    return (

      <Grid container
            className="transaction">
        {small === true &&
        <Paper className="transactionItem">
          <Grid container
                className="rowItem"
                direction="row"
                justify="space-between"
                alignItems="center">
            <Grid container
                  direction="row"
                  alignItems="center"
                  justify="flex-start">
              <span className="date">{formatedDate(date)}</span>
            </Grid>
            <Grid container
                  direction="row"
                  alignItems="center"
                  justify="flex-end">
              {(sender === true) && (receiver === false) && <FontAwesomeIcon className='icon'
                                                                             icon={faLongArrowAltLeft}/>
              }
              {(receiver === true) && (sender === false) && <FontAwesomeIcon className='icon'
                                                                             icon={faLongArrowAltRight}/>
              }
              <span className="count">{count}</span>
              <span className="shortName">{shortName}</span>
            </Grid>
          </Grid>
          <Grid container
                className="rowItem"
                direction="row"
                justify="space-between"
                alignItems="flex-end">
            <Grid>
              <span className="address">{address}</span>
            </Grid>
          </Grid>
        </Paper>}
        {small === false && <Grid className="bigTransactions"
                                  container
                                  direction="row"
                                  wrap="nowrap"
                                  alignItems="center">
          <Grid container
                direction="row"
                wrap="nowrap"
                className="firstRow"
                alignItems="center">
            <Grid container
                  direction="row"
                  className="dateBlock"
                  wrap="nowrap"
                  alignItems="center">
              <span className="date">{formatedDate(date)}</span>
              <Icon className="iconCoin"
                    iconName={shortName}/>
            </Grid>
            <span className="address">
                              {(sender === true) && (receiver === false) && <FontAwesomeIcon className='icon'
                                                                                             icon={faLongArrowAltLeft}/>
                              }
              {(receiver === true) && (sender === false) && <FontAwesomeIcon className='icon'
                                                                             icon={faLongArrowAltRight}/>
              }
              {address}</span>
          </Grid>
          <Grid container
                className="priceData"
                direction="row"
                justify="flex-end"
                alignItems="center">
            <span className="count">{count}<span className="shortName">{shortName}</span></span>
            {status === true &&
            <Button variant="contained"
                    className="completed">
              Completed
              <FontAwesomeIcon className='iconInBtn'
                               icon={faCheck}/>
            </Button>
            }
            {status === false &&
            <Button variant="contained"
                    className="pending">
              Pending
              <FontAwesomeIcon className='iconInBtn'
                               icon={faHistory}/>
            </Button>
            }
          </Grid>
        </Grid>}
      </Grid>
    );
  }
}

export default Transaction;
