import '../CardForm/CardForm.scss';
import React from 'react';
import {Button, Grid} from "@material-ui/core";


const AddressInfo = ({handleChange,onSubmit})=>{

    return(
        <form className='card-form' onSubmit={onSubmit}>
            <label>COUNTRY</label>
            <input name='country' required onChange={handleChange}/>
            <label>Address(1)</label>
            <input name="address"  required onChange={handleChange}/>
            <label>Address(2)</label>
            <input name="address2" required onChange={handleChange}/>
            <Grid className="city-postal" container item spacing={8}>
                <Grid item xs={6}>
                    <label>CITY</label>
                    <input name='city' required onChange={handleChange}/>
                </Grid>
                <Grid item xs={6}>
                    <label>POSTAL CODE</label>
                    <input name="postalCode" required onChange={handleChange}/>
                </Grid>
            </Grid>
            <Button type='submit' className="card-form-button">Continue</Button>
        </form>
    )
};

export default AddressInfo;
