import {
    CardCVCElement,
    CardExpiryElement,
    CardNumberElement,
    injectStripe,
} from "react-stripe-elements";
import './CardForm.scss'
import React from "react";
import {toast} from "react-toastify";
import {Button, Grid} from "@material-ui/core";
import Visa from '../../../../../../images/img-fi-visa.png';
import MasterCard from '../../../../../../images/img-fi-mastercard.png'


class CardForm extends React.Component{

    submit = async(event) => {
        event.preventDefault();
        const {city,country,postalCode,address,address2,name} =this.props.data;
        let {token} = await this.props.stripe.createToken(
            {
                name:name,
                address_city:city,
                address_country:country,
                address_zip:postalCode,
                address_line1:address,
                address_line2:address2
            });
        let response = await fetch("/charge", {
            method: "POST",
            headers: {"Content-Type": "text/plain"},
            body: token.id
        });
        if (response.ok){
            this.props.onSubmit(event,2);
            toast.success("Your card successfully added");
        }
        else {
            this.props.onSubmit()
        }
    };

    render(){
        const {handleChange}= this.props;
        return(
            <form className='card-form'>
                <label >Name</label>
                <input type="text" className="name" name='name' required onChange={handleChange}/>
                <label >Card Number</label>
                <Grid container style={{position:"relative"}}>
                    <Grid className='icons' >
                        <img alt='icon' src={Visa}/>
                        <img alt='icon' src={MasterCard}/>
                    </Grid>
                    <CardNumberElement
                        className='card-number'
                    />
                </Grid>
                <Grid className="half-clock">
                    <Grid className="security-wrapper" >
                        <Grid container className="security">
                            <label>EXPIRY DATE</label>
                            <CardExpiryElement className='cvc'/>
                        </Grid>
                        <Grid container className="security cvc-clock">
                            <label>CVC</label>
                            <CardCVCElement className='cvc'/>
                        </Grid>
                    </Grid>
                    <Grid className="postal">
                        <label>POSTAL CODE</label>
                        <input type='number ' className='name'/>
                    </Grid>
                </Grid>
                <Button className="card-form-button" onClick={this.submit}>Continue</Button>
                <p>
                    By adding a card, you accept
                    <a href="#"> The General Conditions of use of The Credit Card.</a>
                </p>
            </form>
        )
    }
}

export default injectStripe(CardForm);

