import * as React from "react";
import {connect} from "react-redux";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import MarketCalculator from '../MarketCalculator/MarketCalculator';

import {Link} from "react-router-dom";

class MarketCalculatorList extends React.Component {
  render() {
    const {coins} = this.props;
    return (
      <Grid className="marketList marketListCalculator">
        <Grid container
              direction="row"
              item xs={6}
              alignItems="flex-start">
          <Grid container
                className="marketItems"
                justify="space-between">
            {Object.keys(coins).map((key,id) => {
              const item = coins[key];
              return (
                <Link to={`/trading/${item.shortName}`} key={id} className="linkToTranding">
                  <MarketCalculator
                    key={item.shortName}
                    coint={item.coint}
                    currency={item.shortName}
                    shortName={item.shortName}
                    amount={item.amount}
                    markets={item.markets}
                    fullName={item.fullName}
                    percentage="1.14%"/></Link>
              )
            })}
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  coins: store.wallets.coins
});
export default connect(mapStateToProps)(MarketCalculatorList);
