import React from 'react'
import Profile from "./components/Settings/SettingsMain";
import "./index.scss";
import {Grid} from "@material-ui/core";
import SettingsList from "./components/Settings/SettingsList/SettingsList";
import {Route} from 'react-router-dom'
import Security from './components/Settings/Security/Security';
import Notification from './components/Settings/Notification/Notification';
import Referrals from './components/Settings/Referrals/Referrals';
import Email from './components/Settings/Email/Email';
import Sessions from './components/Settings/Sessions/Sessions';

class Settings extends React.Component {


  render() {
    return (
      <Grid className="Settings" item justify="space-between" container>
          <Route exact path="/settings" component={Profile}/>
          <Route exact path="/settings/security" component={Security}/>
          <Route exact path="/settings/notification" component={Notification}/>
          <Route exact path="/settings/referrals" component={Referrals}/>
          <Route exact path="/settings/email" component={Email}/>
          <Route exact path="/settings/sessions" component={Sessions}/>
        <SettingsList />
      </Grid>
    );
  }

}

export default Settings
