import React from 'react';
import BTC from '../../../../images/BTC.png';
import ETH from '../../../../images/ETH.png';
import LTC from '../../../../images/LTC.png';
import OZTG from '../../../../images/OZTG.png';
import DASH from '../../../../images/DASH.png';
import RPL from '../../../../images/RPL.png';

class Icon extends React.Component {

  getIcon() {
    if (this.props.iconName === 'BTC') {
      return BTC;
    }
    if (this.props.iconName === 'ETH') {
      return ETH;
    }
    if (this.props.iconName === 'LTC') {
      return LTC;
    }
    if (this.props.iconName === 'OZTG') {
      return OZTG;
    }

    if (this.props.iconName === 'DASH') {
      return DASH;
    }
    if (this.props.iconName === 'RPL') {
      return RPL;
    }
  }

  render() {
    return (
      <img src={this.getIcon()} alt={this.props.iconName}/>
    )
  }

}

export default Icon;
