import * as React from 'react';
import PropTypes from 'prop-types';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Icon from "../../../transactions/components/Icon/Icons";

const defaultProps = {
  shortName: '',
  fullName: '',
  imageName: '',
  coint: null,
  active: false,
  id: 0
};


const propTypes = {
  shortName: PropTypes.string,
  fullName: PropTypes.string,
  imageName: PropTypes.string,
  coint: PropTypes.number,
  active: PropTypes.bool,
  selected: PropTypes.number
};

class Wallet extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {shortName, fullName, price, active, selected} = this.props;
    return (
      <Grid container
            className={active === true ? 'wallet active' : 'wallet'}
            onClick={() => this.props.onClick(selected)}>
        <Paper className="walletItem">
          <Grid container
                className="rowItem"
                direction="row"
                justify="space-between"
                alignItems="center">
            <span className="shortName">{shortName}</span>
            <Icon iconName={shortName}/>
          </Grid>
          <Grid container
                className="rowItem"
                direction="row"
                justify="space-between"
                alignItems="flex-end">
            <span className="fullName">{fullName}</span>
            {active === true &&
            <span className="coint">{price}</span>
            }
          </Grid>
        </Paper>
      </Grid>
    );
  }
}

export default Wallet;
