import * as React from 'react';
import '../BuySell/style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import AppBar from '@material-ui/core/AppBar/AppBar';
import Tabs from '@material-ui/core/Tabs/Tabs';
import Tab from '@material-ui/core/Tab/Tab';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography/Typography';
import CardInfo from './Card'
import BankAccount from './BankAccount/BankAccount';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faTimes} from '@fortawesome/free-solid-svg-icons';

function TabContainer(props) {
    return (
        <Typography component="div">
            {props.children}
        </Typography>
    );
}

TabContainer.propTypes = {
    children: PropTypes.node.isRequired,
};

class Payments extends React.Component {
    state = {
        value: 0,
    };

    handleChangeTab = (event, value) => {
        this.setState({value});
    };

    render() {
        const {value} = this.state;
        return (
            <Grid className="buy-sell-wrapper card-bank-wrapper">
                <h2 className="title">Add Payment Method</h2>
                <Grid className="tab-content">
                    <Paper className="wrapper">
                        <AppBar position="static">
                            <Tabs value={value}
                                  onChange={this.handleChangeTab}>
                                <Tab  className="card-tab" label="Card"/>
                                <Tab className="bank-tab" label="Bank Account" />
                            </Tabs>
                            <FontAwesomeIcon icon={faTimes}
                                             className="close-icon" />
                        </AppBar>
                        {value === 0 && <TabContainer>
                            <Grid container
                                  className="tabContainer"
                                  direction="row"
                                  justify="flex-start"
                                  alignItems="center">
                                <Grid container
                                      className="form-wrapper"
                                      direction="column"
                                      alignItems="center">
                                    <CardInfo/>
                                </Grid>
                            </Grid>
                        </TabContainer>}
                        {(value === 1) && <TabContainer>
                            <Grid container
                                  className="tabContainer"
                                  direction="row"
                                  justify="flex-start"
                                  alignItems="center">
                                <Grid container
                                      direction="column"
                                      alignItems="center">
                                      <BankAccount/>
                                </Grid>
                            </Grid>
                        </TabContainer>}
                    </Paper>
                </Grid>
            </Grid>
        );
    }
}


export default Payments;

