import * as types from "../constants/getcoins";
import {toast} from "react-toastify";
import Config from "../config";

export function getCoins(currency) {
    return (dispatch) => {
        dispatch({
            type: types.GETCOINS_REQUEST,
        });
        fetch(`${Config.serverUrl}/v1/currencies/values/${currency}`, {
        })
            .then(res => res.json())
            .then(json=>{
                dispatch({
                    type:types.GETCOINS_SUCCESS,
                    payload:json.data
                });
            })
            .catch(json => {
                toast.error('Server error');
                dispatch({
                    type: types.GETCOINS_FAILURE,
                    payload: json.message
                })
            });
    };
}
