import React, {Component} from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button/Button';
import copy from '../../../../../../images/copy.png'

class Deposit extends Component {
    state = {
        referenceNumber: ''
    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };


    render() {
        return (
            <Grid className="deposit">
                <form className='card-form'
                      onSubmit={this.props.onSubmit}>
                    <Grid className="content">
                        <Grid container
                              className="message-block"
                              direction="column">
                            <p><b>Please include this reference code in your transfer.</b></p>
                            <p>
                                Without this code, we cannot deposit these funds into
                                your FlashWallet account.
                            </p>
                        </Grid>
                        <Grid container
                              className="referenceInput"
                              direction="column">
                            <span className="label">REFERENCE NUMBER</span>
                            <input
                                type="text"
                                name="referenceNumber"
                                required
                                value={this.state.referenceNumber}
                                onChange={this.handleChange} />
                            <img src={copy} alt='icon' className="copy-icon"/>
                        </Grid>
                        <Grid container
                              wrap="nowrap"
                              className="reference-block"
                              justify="flex-start"
                              alignItems="center"
                              direction="row">
                            <input type="checkbox"
                                   id="referenceNumber"
                                   className="checkbox"
                                   name="referenceNumber" />
                            <label for="referenceNumber">Ive copied or written down my Reference Number</label>
                        </Grid>
                        <Grid>
                            <Button variant="contained"
                                    type='submit'
                                    className="continueBtn">
                                Continue
                            </Button>
                        </Grid>
                    </Grid>
                </form>
            </Grid>
        );
    }
}

export default Deposit;

