import React, {Component} from 'react';
import './CardInfo.scss';
import CardForm from './CardForm';
import AddressInfo from './AddressInfo';
import {StripeProvider,Elements} from "react-stripe-elements";
import CardResult from './CardResult';
import Success from '../../../../../images/Star.png';
import Failure from '../../../../../images/Warning.png';

const result={
  success:{
      img:Success,
      text:'Your card has been successfully associated!',
      processed:'Processed by ',
      link:'FlashWallet'

  },
  failure:{
      img:Failure,
      text:"Oops something's wrong",
      processed:'Processed by ',
      link:'FlashWallet'
  }
};

class CardInfo extends Component {
    state = {
        complete: 1,
        cardInfo:{
            name:'',
            country:'',
            address:'',
            address2:'',
            city:'',
            postalCode:''
        },
    };

    onSubmit=(event,value = 1)=>{
        console.log('value', value);
        const  increment = this.state.complete + value;
        this.setState({complete: increment });
    };

    handleChange = event => {
        this.setState({cardInfo:{...this.state.cardInfo,[event.target.name]: event.target.value}});
    };

    render() {
        console.log(this.state.complete);
        let data = this.state.complete===4 ? result.success:  result.failure;
        return (
            <StripeProvider apiKey="pk_test_vENWBb1X8lla9XYuLqtXM7uC00CpWRytIV">
                <div className="checkout">
                    {
                        this.state.complete ===1 ? <AddressInfo handleChange={this.handleChange}
                                onSubmit={this.onSubmit}/>
                        :this.state.complete === 2 ?
                            <Elements>
                                <CardForm
                                    data={this.state.cardInfo}
                                    onSubmit={this.onSubmit}
                                    handleChange={this.handleChange}/>
                            </Elements>
                        : <CardResult data={data}/>
                    }
                </div>
            </StripeProvider>
        );
    }
}

export default  CardInfo;
