import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper/Paper';
import Typography from '@material-ui/core/Typography/Typography';
import PropTypes from 'prop-types';
import MarketList from '../MarketList/MarketList';
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {getMarkets} from '../../../../actions/markets';
import {Button} from "@material-ui/core";

function TabContainer(props) {
  return (
    <Typography component="div">
      {props.children}
    </Typography>
  );
}

TabContainer.propTypes = {
  children: PropTypes.node.isRequired,
};

const defaultProps = {
  small: false
};


const propTypes = {
  small: PropTypes.bool
};


const currencies=['usd','eur','oztg'];

class MarketWrapper extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  state = {
    currentValue: 'usd',
    activeButtons:0,
  };

  componentDidMount() {
    this.props.getMarkets(this.state.currentValue);
  }

  onChange = (event,item,id) => {
    event.preventDefault();
    this.setState({
      currentValue:item,
      activeButtons:id
    });
    this.props.getMarkets(item);
  };


  render() {
    const {small, coins} = this.props;
    return (
      <Grid className="marketWrapper"
            container
            direction="row">
        {small === true &&
        <Grid style={{flex: "1 1"}}>
          <h1 className="title">Markets</h1>
          <Paper className="smallMarket" >
              <Grid className="tabsView" container justify='flex-end'>
                      {currencies.map((item, id) => {
                        if(id===this.state.activeButtons){
                    return (
                          <Button onClick={event=>this.onChange(event,item,id)}  className="selected-button" key={id}> {item} </Button>
                     )}
                        else {
                          return (
                            <Button onClick={event=>this.onChange(event,item,id)}  className="market-button" key={id}> {item} </Button>
                        )}
                  })}
              </Grid>
              <MarketList className="marketList" currentValue={this.state.currentValue} coins={coins} small={small}/>
          </Paper>
        </Grid>
        }
        {small === false && <Grid style={{width: "100%"}}>
          <Grid className="marketBigWrapper">
            <MarketList coins={coins}/>
          </Grid>
        </Grid>}
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  coins: store.wallets.coins
});

const mapDispatchToProps = dispatch=>bindActionCreators({
  getMarkets
},dispatch);

export default connect(mapStateToProps,mapDispatchToProps)(MarketWrapper);
