import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import WalletDetail from '../WalletDetail/WalletDetail';
import WalletInfo from '../WalletInfo/WalletInfo';
import {Link, Route} from 'react-router-dom'
import connect from 'react-redux/es/connect/connect';

class WalletDetailsList extends React.Component {
  render() {
    const {wallets} = this.props;
    return (
      <Grid className="walletListDetails">
        <Grid container
              direction="row"
              justify="flex-start"
              alignItems="stretch">
          {Object.keys(wallets.coins).map(function (key, id) {
            const item = wallets.coins[key];
            return <Grid className="transactionItems" key={id}>
              <Grid className="walletsItems">
                <Link to={`/wallet-info/${key}`}
                      className="linkToInfo">
                  <WalletDetail
                      fullName={item.fullName}
                      shortName={item.shortName}
                      imageName={item.imageName}
                      count={item.count}
                      markets={item.markets}/>
                </Link>
              </Grid>
            </Grid>
          })}
          <Grid className="walletsItems addNewItem ">
            <Grid className="addWrapper"
                  container
                  direction="column"
                  justify="center">
              <p className="addPlus"></p>
              <p className="addWallet">Add wallet</p>
            </Grid>
          </Grid>
        </Grid>

        <Route exact
               path="/wallet-info"
               component={WalletInfo}/>
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  wallets: store.wallets
});

export default connect(mapStateToProps)(WalletDetailsList);
