const initialState = {
  settings: {
      picture: '',
      address: '1Cs4wu6pu5qCZ35bSLNVzGyEx5N6uzbg9t',
      firstname: 'Serge',
      lastname: 'Lobrau',
      email: 'sergelobreau@gmail.com',
      username: 'Serge Lobrau',
      date: '2',
      month: '3',
      year: '1997'
  }
};

export default (state = initialState, action) => {
  switch (action.type) {
    case "user/SET_PERSONAL_DETAILS": {
      const {payload} = action;
      return {...state, settings: {...state.settings, ...payload}};
    }
    default:
      return state
  }
}

