import * as React from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import Transaction from '../Transaction/Transaction';
import PropTypes from 'prop-types';

const defaultProps = {
  small: false,
  transactions: []
};

const propTypes = {
  small: PropTypes.bool,
  transactions: PropTypes.array
};

class TransactionItems extends React.Component {
  static defaultProps = defaultProps;
  static propTypes = propTypes;

  render() {
    const {small, transactions} = this.props;
    return (
      <Grid className="transactionList">
        <Grid container
              direction="column"
              alignItems="center">

          {transactions.length > 0 ? transactions.map(function (item, key) {
            return <Grid
              key={key}
              className="transactionItems">
              <Transaction imageName={item.imageName}
                           shortName={item.shortName}
                           date={item.date}
                           small={small}
                           address={item.address}
                           status={item.status}
                           sender={item.sender}
                           receiver={item.receiver}
                           count={item.count}/>
            </Grid>
          }) : (
            <div> No transactions </div>
          )}
        </Grid>
      </Grid>
    );
  }
}

export default TransactionItems;
