import React from 'react';
import NewsVideo from './NewsVideo/NewsVideo'
import './style.scss'
import Grid from "@material-ui/core/Grid";
import ReadMore from "./ReadMore/ReadMore";
import ReadMore2 from "./ReadMore2/ReadMore2";
import NewsDashBoard from "./NewsDashboard/NewsDashBoard";
import NewsScience from "./NewsScience/NewsScience";



class NewsMain extends React.Component {


  render() {
    const {news} = this.props;
    return (
      <Grid className='NewsMain' container wrap='wrap'>
        <Grid item xs={9} container wrap='wrap' className="firstSection">
          <Grid item xs={4} className="first">
            <NewsVideo newsVideo={news.newsVideo[0]}/>
          </Grid>
          <Grid item xs={8} className="first">
            <ReadMore readMore={news.readMore[0]}/>
          </Grid>
          <Grid item xs={9} className="readMore2">
            <ReadMore2 readMore2={news.readMore2[0]}/>
          </Grid>
          <Grid item xs={3} className="newScience">
            <NewsScience newsScience={news.science[0]}/>
          </Grid>
        </Grid>
        <Grid item xs={3} className="secondSection">
          <NewsDashBoard dashBoard={news.dashboards[0]}/>
        </Grid>
      </Grid>
    )
  }

}


export default NewsMain;
