import * as React from "react";
import {connect} from 'react-redux';
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faInfoCircle, faUser} from '@fortawesome/free-solid-svg-icons';
import {logout} from "../../../../actions/auth";
import {Link, Redirect} from 'react-router-dom';
import logOut from '../../../../images/log-out.png'
import {bindActionCreators} from "redux";
import jwt from 'jsonwebtoken';


class User extends React.Component {
  constructor(props) {
    super(props);


    const { username, firstname } = jwt.decode(localStorage.getItem("token")).user;

    this.state = {
      username,
      firstname,
    }
  }

  logOut = (event) => {
    event.preventDefault();
    this.props.logout(localStorage.getItem('token'));
    return <Redirect to='/main'/>
  };


  render() {
    const {firstname, username} = this.state;
    return (
      <Grid container
            className="user"
            direction="row"
            alignItems="center">
        <Button variant="contained" className="supportBtn">
          <FontAwesomeIcon className='iconInfo' icon={faInfoCircle}/>
          Support
        </Button>
        <Avatar
          component={Link}
          to="/settings"
          className="avatar">
          <FontAwesomeIcon className='icons' icon={faUser}/>
        </Avatar>
        <p className="userName">Hello {`${username ? username : ""} ${firstname ? firstname : ""}`}</p>
        <Button onClick={this.logOut}>
          <img alt='logo' src={logOut}/>
        </Button>
      </Grid>
    );
  }
}

const mapStateToProps = function (state) {
  return {
    firstname: state.user.settings.firstname,
    lastname: state.user.settings.lastname,
  }
};

const mapDispatchToProps = dispatch => bindActionCreators({
  logout
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(User);
