import React, {Component} from 'react';
import './style.scss';
import Grid from '@material-ui/core/Grid';
import connect from 'react-redux/es/connect/connect';
import PropTypes from 'prop-types';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem/MenuItem';
import Button from '@material-ui/core/Button/Button';

class WireTransfert extends Component {
    state = {
        complete: false,
        bankName:'',
        accountName:'',
        amount: 0,
        nameCurrency: 'BTC'
    };

    handleChange = event => {
        this.setState({[event.target.name]: event.target.value});
    };

    render() {
        const {coins} = this.props;
        return (
            <Grid className="wire-transfert">
             <form className='card-form' onSubmit={this.props.onSubmit}>
              <Grid className="content">
                    <Grid container
                        direction="column">
                        <span className="label">NAME OF BANK</span>
                        <input
                            type="text"
                            name="bankName"
                            required
                            value={this.state.bankName}
                            onChange={this.handleChange}/>
                    </Grid>
                    <Grid container
                        direction="column">
                        <span className="label">NAME ON ACCOUNT</span>
                        <input
                            type="text"
                            name="accountName"
                            required
                            value={this.state.accountName}
                            onChange={this.handleChange}/>
                    </Grid>
                    <Grid container
                        justify="space-between"
                        className="selectandinput"
                        wrap="nowrap"
                        direction="row">
                        <Grid container
                            direction="column">
                            <span className="label">SEPA TRANSFERT AMOUNT</span>
                            <input
                                type="number"
                                name="amount"
                                required
                                value={this.state.amount}
                                onChange={this.handleChange}/>
                        </Grid>
                        <Grid container
                            direction="column">
                            <span className="label">CURRENCY</span>
                            <Select
                                className="selectCurrency"
                                value={this.state.nameCurrency}
                                onChange={this.handleChange}
                                inputProps={{
                                    name: 'nameCurrency'
                                }}>
                                {Object.keys(coins).map((key, id) => {
                                    const item = coins[key];
                                    return <MenuItem className="labelName"
                                                        key={id}
                                                        value={item.shortName}>
                                        {item.shortName}
                                    </MenuItem>
                                })}
                            </Select>
                        </Grid>
                    </Grid>
                      <Grid>
                            <Button variant="contained"
                                    type='submit'
                                    className="continueBtn">
                                Continue
                            </Button>
                        </Grid>
                </Grid>
                </form>
            </Grid>
        );
    }
}

const mapStateToProps = store => ({
    coins: store.wallets.coins
});

export default connect(mapStateToProps)(WireTransfert);

