//   },
//   {
//     name: '6 nov', uv: 3000, pv: 1398, amt: 2210,
//   },
//   {
//     name: '20 nov', uv: 2000, pv: 9800, amt: 2290,
//   },
//   {
//     name: '4 dec', uv: 2780, pv: 3908, amt: 2000,
//   },
//   {
//     name: '21 dec', uv: 1890, pv: 4800, amt: 2181,
//   },
//   {
//     name: '12 jan', uv: 2390, pv: 3800, amt: 2500,
