class Tranding extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedValue: TIME_IN_MS.ONE_MONTH,
      coin: 'BTC',
    };
  }

  handleChange = event => {
    this.setState({[event.target.name]: event.target.value});
  };

  componentDidMount() {
    getData().then(data => {
      this.setState({data})
    })
  }

  render() {
    const {coins} = this.props;
    return (
      <Grid className="tranding"
            container
            justify="space-between"
            alignItems="flex-start"
            direction="row">
        <Grid className="orderBook">
          <FormControl className="selectName">
            <Select
              value={this.state.coin}
              onChange={this.handleChange}
              inputProps={{
                name: 'coin'
              }}>
              {Object.keys(coins).map((key) => {
                const item = coins[key];
                return <MenuItem key={item.shortName} value={key}>
                  {item.shortName}/USD
                </MenuItem>
              })}
            </Select>
          </FormControl>
          <Paper className="wrapper">
            <Grid container
                  direction="row"
                  alignItems="center"
                  className="banner"
                  justify="space-between">
              <h1 className="title">Order book</h1>
              <Grid container
                    direction="row"
                    justify="flex-end"
                    className='btnGroup'>
                <FontAwesomeIcon icon={faPlusCircle}/>
                <FontAwesomeIcon icon={faMinusCircle}/>
              </Grid>
            </Grid>
            <Table className="table">
              <TableBody className="bodyTable">
                {rows.map(row => (
                  <TableRow className="row" key={row.id}>
                    <TableCell className="whiteColumn column" align="left">  {row.name}</TableCell>
                    <TableCell className="redColumn column" align="right">{row.calories}</TableCell>
                    <TableCell className="greyColumn column" align="right">{row.fat}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
            <Grid container
                  className="blockWrapper"
                  direction="row"
                  justify="center"
                  alignItems="center">
              <span className="count">12,234.00</span>
              <span className="currency">USD</span>
            </Grid>
            <Table className="table">
              <TableBody className="bodyTable">
                {rows.map(row => (
                  <TableRow className="row" key={row.id}>
                    <TableCell className="whiteColumn column" align="left">  {row.name}</TableCell>
                    <TableCell className="greenColumn column" align="right">{row.calories}</TableCell>
                    <TableCell className="greyColumn column" align="right">{row.fat}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
        <Grid className="priceChart">
          <h1 className="title big">Price Chart</h1>
          <Paper className="wrapper chart">
            <Grid className="radioBtnGroup"
                  container
                  alignItems="center"
                  justify="flex-end"
                  direction="row">
              <label>
                <input type="radio"
                       name="radio-button"
                       value={TIME_IN_MS.ONE_MONTH}
                       onChange={this.handleChange}
                       aria-label="1m"
                       checked={this.state.selectedValue === TIME_IN_MS.ONE_MONTH}/>
                <span>1m</span>
              </label>
              <label>
                <input type="radio"
                       name="radio-button"
                       value={TIME_IN_MS.THREE_MONTH}
                       onChange={this.handleChange}
                       aria-label="3m"
                       checked={this.state.selectedValue === TIME_IN_MS.THREE_MONTH}/>
                <span>3m</span>
              </label>
              <label>
                <input type="radio"
                       name="radio-button"
                       value={TIME_IN_MS.SIX_MONTH}
                       onChange={this.handleChange}
                       aria-label="6m"
                       checked={this.state.selectedValue === TIME_IN_MS.SIX_MONTH}/>
                <span>6m</span>
              </label>
              <label>
                <input type="radio"
                       name="radio-button"
                       value={TIME_IN_MS.ONE_YEAR}
                       onChange={this.handleChange}
                       aria-label="1y"
                       checked={this.state.selectedValue === TIME_IN_MS.ONE_YEAR}/>
                <span>1y</span>
              </label>
              <label>
                <input type="radio"
                       name="radio-button"
                       value={TIME_IN_MS.ALL}
                       onChange={this.handleChange}
                       aria-label="All"
                       checked={this.state.selectedValue === TIME_IN_MS.ALL}/>
                <span>All</span>
              </label>
            </Grid>
            {
              this.state.data ? (
                <Stockchart className="chartStock" width="700" data={this.state.data}/>
              ) : null
            }
          </Paper>
        </Grid>

        <Grid className="ordersHistory">
          <h1 className="title history">Orders history</h1>
          <Paper className="wrapper">
            <Table className="table">
              <TableHead>
                <TableRow className="tableRow">
                  <TableCell className="head">Trade Size</TableCell>
                  <TableCell className="head" align="right">Price (BTC)</TableCell>
                  <TableCell className="head" align="right">Time</TableCell>
                </TableRow>
              </TableHead>
              <TableBody className="bodyTable">
                {rows.map(row => (
                  <TableRow className="row" key={row.id}>
                    <TableCell className="whiteColumn column" align="left">{row.name}</TableCell>
                    <TableCell className="greenColumn column" align="right">{row.calories}</TableCell>
                    <TableCell className="greyColumn column" align="right">{row.fat}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
      </Grid>
    );
  }
}

const mapStateToProps = store => ({
  coins: store.wallets.coins
});


export default connect(mapStateToProps)(Tranding);

