import React from 'react'
import PropTypes from 'prop-types';
import {Link} from 'react-router-dom'
import Calculator from '../calculator';
import Dashboard from '../dashboard';
import Events from '../events';
import Markets from '../markets';
import News from '../news';
import Settings from '../settings';
import Transactions from '../transactions';
import Wallets from '../wallets';
import Payments from '../dashboard/components/Payments/Payments';
import './style.scss';

import Header from './components/Header/Header';

import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import {withStyles} from '@material-ui/core/styles';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Drawer from '@material-ui/core/Drawer';

import logo from '../../images/logo.png';
import dashboardIcon from '../../images/dashboard-icon.png';
import marketsIcon from '../../images/markets-icon.png';
import walletsIcon from '../../images/wallets-icon.png';
import newsIcon from '../../images/news-icon.png';
import eventsIcon from '../../images/events-icon.png';
import calculatorIcon from '../../images/calculator-icon.png';
import settingsIcon from '../../images/settings-icon.png';
import transactionIcon from '../../images/transaction-icon.png';
import WalletInfo from '../wallets/components/WalletInfo/WalletInfo';
import Tranding from '../markets/components/Tranding/Tranding';
import PrivateRoute from "../../app/PrivateRoute";
import {Payment} from "@material-ui/icons";
import BuySell from '../dashboard/components/BuySell/BuySell';

const styles = theme => ({
  menuItem: {
    '&:focus': {
      backgroundColor: theme.palette.primary.main,
      '& $primary, & $icon': {
        color: theme.palette.common.white,
      },
    },
  },
  primary: {},
  drawer: {
    width: 200
  }
});

const App = (props) => {
  const {classes} = props;

  return (
    <div>
      <Drawer
        className="drawer"
        variant="permanent"
        anchor="left"
        open>
        <MenuList>
          <Link to="/main"
                className="logo"><img alt='logo' src={logo}/></Link>
          <MenuItem
            component={Link}
            to="/main"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={dashboardIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/wallets"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={walletsIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/markets"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={marketsIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/events"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={eventsIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/transactions"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={transactionIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/calculator"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={calculatorIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/news"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={newsIcon}/>
            </ListItemIcon>
          </MenuItem>
          <MenuItem
            component={Link}
            to="/settings"
            className={classes.menuItem}>
            <ListItemIcon>
              <img alt='logo' src={settingsIcon}/>
            </ListItemIcon>
          </MenuItem>
        </MenuList>
      </Drawer>

      <Header/>

      <main className="main">
        <PrivateRoute exact
                      path="/(main)?"
                      component={Dashboard}/>
          <PrivateRoute path="/main/buy-sell"
                 component={BuySell}/>
        <PrivateRoute exact
                      path="/calculator"
                      component={Calculator}/>
        <PrivateRoute exact
                      path="/events"
                      component={Events}/>
        <PrivateRoute exact
                      path="/transactions"
                      component={Transactions}/>
        <PrivateRoute exact
                      path="/wallets"
                      component={Wallets}/>
        <PrivateRoute exact
                      path="/wallet-info/:type"
                      component={WalletInfo}/>
        <PrivateRoute exact
                      path="/markets"
                      component={Markets}/>
        <PrivateRoute exact
                      path="/trading/:type"
                      component={Tranding}/>
        <PrivateRoute exact
                      path="/news"
                      component={News}/>
        <PrivateRoute path="/settings"
                      component={Settings}/>
        <PrivateRoute path='/payments'
                      component = {Payments}/>
      </main>
    </div>
  )
};

App.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(App);
