import React from 'react'
import User from '../User/User';
import './style.scss';
import PropTypes from 'prop-types';
import Grid from '@material-ui/core/Grid';
import {Link, Redirect, withRouter} from 'react-router-dom';
import logo from '../../../../images/logo.png';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faArrowRight, faBars} from '@fortawesome/free-solid-svg-icons';
import logOutIcon from '../../../../images/log-out.png'
import {logout} from '../../../../actions/auth';


import MenuList from '@material-ui/core/MenuList';
import MenuItem from '@material-ui/core/MenuItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import Drawer from '@material-ui/core/Drawer';

import marketsIcon from '../../../../images/markets-icon.png';
import walletsIcon from '../../../../images/wallets-icon.png';
import newsIcon from '../../../../images/news-icon.png';
import eventsIcon from '../../../../images/events-icon.png';
import calculatorIcon from '../../../../images/calculator-icon.png';
import settingsIcon from '../../../../images/settings-icon.png';
import transactionIcon from '../../../../images/transaction-icon.png';
import dashboardIcon from '../../../../images/dashboard-icon.png';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';


const defaultProps = {
    title: ''
};

const propTypes = {
    title: PropTypes.string
};

const headerPathNames = {
    '/main': 'Dashboard',
    '/main/buy-sell': 'Dashboard',
    '/wallets': 'Wallets',
    '/wallet-info/oztg': 'OZTG Wallet info',
    '/wallet-info/bitcoin': 'BTC Wallet info',
    '/wallet-info/ethereum': 'ETH Wallet info',
    '/wallet-info/ripple': 'RPL Wallet info',
    '/wallet-info/litecoin': 'LTC Wallet info',
    '/wallet-info/dash': 'DASH Wallet info',
    '/wallet-info/dollar': 'USD Wallet info',
    '/markets': 'Markets',
    '/trading/OZTG': 'OZTG Trading',
    '/trading/BTC': 'BTC Trading',
    '/trading/ETH': 'ETH Trading',
    '/trading/RPL': 'RPL Trading',
    '/trading/LTC': 'LTC Trading',
    '/trading/DASH': 'DASH Trading',
    '/trading/USD': 'USD Trading',
    '/events': 'Events',
    '/transactions': 'Transactions',
    '/calculator': 'Calculator',
    '/news': 'News',
    '/settings': 'Settings',
    '/settings/profile': 'Profile Settings',
    '/settings/security': 'Security Settings',
    '/settings/notification': 'Notification Settings',
    '/settings/referrals': 'Referrals',
    '/settings/email': 'Email Settings',
    '/settings/sessions': 'Sessions',
};

class Header extends React.Component {
    static defaultProps = defaultProps;
    static propTypes = propTypes;

    constructor(props) {
        super(props);
        this.state = {
            showMobMenu: false
        };

        this.showMenu = this.showMenu.bind(this);
        this.hideMenu = this.hideMenu.bind(this);
    }

    showMenu() {
        this.setState({showMobMenu: true});
    };

    hideMenu() {
        this.setState({showMobMenu: false});
    };

    logOut = (event) => {
        event.preventDefault();
        this.props.logout();
        return <Redirect to='/main' />
    };

    render() {
        const {location: {pathname}} = this.props;
        return (
            <div className="header">
                <Grid className="mobMenu"
                      container
                      justify="space-between"
                      alignItems="center"
                      direction="row">
                    <Grid><Link to="/"
                                className="logo"><img alt='logo'
                                                      src={logo} /></Link></Grid>
                    <Grid onClick={this.showMenu}><FontAwesomeIcon className='iconMenu'
                                                                   icon={faBars} /></Grid>
                </Grid>
                {this.state.showMobMenu === true && <Grid>
                    <Drawer
                        className="drawer activeDrawer"
                        variant="permanent"
                        anchor="left"
                        open>
                        <MenuList>
                            <MenuItem onClick={this.hideMenu}>
                                <FontAwesomeIcon className='iconMenu'
                                                 icon={faArrowRight} />
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/main">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={dashboardIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/wallets">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={walletsIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/markets">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={marketsIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/events">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={eventsIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/transactions">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={transactionIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/calculator">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={calculatorIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/news">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={newsIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem
                                component={Link}
                                onClick={this.hideMenu}
                                to="/settings">
                                <ListItemIcon>
                                    <img alt='logo'
                                         src={settingsIcon} />
                                </ListItemIcon>
                            </MenuItem>
                            <MenuItem onClick={this.logOut}>
                                <img alt='logo'
                                     src={logOutIcon} />
                            </MenuItem>
                        </MenuList>
                    </Drawer>
                </Grid>}
                <Grid container
                      className="headerContent"
                      direction="row"
                      alignItems="center"
                      justify="space-between">
                    <Grid>
                        <h1 className="mainTitle">{headerPathNames[pathname]}</h1>
                    </Grid>
                    <Grid>
                        <User />
                    </Grid>
                </Grid>
            </div>
        );
    }
}

const mapStateToProps = store => ({});


const mapDispatchToProps = dispatch => bindActionCreators({
    logout
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)((withRouter)(Header));

