import * as React from "react";
import "./style.scss";
import Grid from '@material-ui/core/Grid';
import CircularProgressbar from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import MoreVertIcon from '@material-ui/icons/MoreVert';

const percentages = [{
  number: 63,
  name: 'OZTG'
},
  {
    number: 25,
    name: 'Ethereum'
  },
  {
    number: 78,
    name: 'Litecoin'
  }];

class Percentage extends React.Component {
  render() {
    return (
      <Grid className="percentage">
        {percentages.map((percentage, i) =>
          <Grid container
                key={i}
                className="rowPerc"
                direction="row"
                alignItems="center">
            <CircularProgressbar
              key={i}
              percentage={percentage.number}
              text={`${percentage.number}%`}/>
            <p key={percentage.number} className="percentCount">{percentage.number}%</p>
            <p key={percentage.name} className="percentName">{percentage.name}</p>
            <Grid className="moreVert">
              <MoreVertIcon/>
            </Grid>
          </Grid>
        )}
      </Grid>
    );
  }
}

export default Percentage;
