import React from "react";
import HeaderMonth from "../HeaderMonth/index";
import HeaderWeekDays from "../HeaderWeekDays/index";
import Day from "../Day/index";
import moment from "moment";
import "./index.scss";
import {connect} from 'react-redux'

class Month extends React.Component {
  state = {
    curMonth: {},
    nextMonth: {},
    prevMonth: {},
    eventsCity: '',
    eventsName: ''
  };

  componentDidMount() {
    this.createState(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.createState(nextProps, true);
  }

  createState(props) {
    const curMonth =
      props.match.params.year && props.match.params.month
        ? `${props.match.params.year}-${props.match.params.month}`
        : moment().format("YYYY-MM");

    const nextMonth = moment(curMonth)
      .add(1, "M")
      .format("YYYY-MM");

    const prevMonth = moment(curMonth)
      .subtract(1, "M")
      .format("YYYY-MM");

    this.setState(
      {
        curMonth: {
          date: curMonth,
          name: moment(curMonth).format("MMMM YYYY"),
          days: moment(curMonth).daysInMonth(),
        },
        nextMonth: {
          date: nextMonth,
          slug: nextMonth.replace("-", "/")
        },
        prevMonth: {
          date: prevMonth,
          slug: prevMonth.replace("-", "/")
        }
      },
      () => {
      }
    );
  }


  buildDays = () => {
    const days = [];
    const props = {};
    const {events} = this.props;

    for (let i = 1; i <= this.state.curMonth.days; i++) {
      let date = `${this.state.curMonth.date}-${("0" + i).slice(-2)}`;
      props["date"] = date;
      props["day"] = i;


      if (i === 1) {
        props["firstDayIndex"] = moment(date)
          .startOf("month")
          .format("d");
      } else {
        delete props["firstDayIndex"];
      }
      const isTrue = events.find((event) => (event.date.substring(0, 10) === date));
      if (isTrue === undefined) {
        days.push(<Day key={i} events={this.state} {...props}/>)
      } else days.push(<Day key={i} events={isTrue} {...props}/>)
    }
    return days;
  };

  render() {
    const weekdays = moment.weekdays();
    const days = this.buildDays();

    return (
      <div className="month">
        <HeaderMonth
          curMonth={this.state.curMonth}
          nextMonth={this.state.nextMonth}
          prevMonth={this.state.prevMonth}
        />
        <HeaderWeekDays days={weekdays}/>
        <section className="days">{days}</section>
      </div>
    );
  }
}

const mapStateToProps = store => ({events: store.events.events});

export default connect(mapStateToProps)(Month)
